/**
 * @author Roger Qi / https://github.com/SouthpawGoblin
 * @description function for generating multi-line text sprite with Three.js.
 * @param {*} text text to show on sprite, use '\n' to seperate lines 
 * @param {*} config {
 *   fontFace: string,
 *   fontSize: number,
 *   fontColor: string(rgba),
 *   fontBold: boolean,
 *   fontItalic: boolean,
 *   textAlign: string,
 *   borderThickness: number,
 *   borderColor: string(rgba),
 *   borderRadius: number,
 *   backgroundColor: string(rgba)
 * } 
 */

import * as THREE from 'three';
import * as Shader from '../shaders';
/**
 * 
 * @param {*} text text to write onto sprite
 * @param {*} config configurations; part of tagInfo
 * @param {*} renderedIdValue should be idValue / 1000
 * @param {*} ruler a canvas element to extract font values
 * @param {*} canvas a canvas element to render content to
 * @param {*} sprite an existing sprite; null if no pre-existing sprite is provided
 */
var generateTextSprite = function(text, config, renderedIdValue, ruler = null, canvas = null, sprite = null) {
    config || (config = { });
  
    var fontFace = Object.prototype.hasOwnProperty.call(config, 'fontFace') ? config['fontFace'] : 'Arial';
    var fontSize = Object.prototype.hasOwnProperty.call(config, 'fontSize') ? config['fontSize'] : 18;
    var fontColor = Object.prototype.hasOwnProperty.call(config, 'fontColor') ? config['fontColor'] : 'rgba(0, 0, 0, 1)';
    var fontBold = Object.prototype.hasOwnProperty.call(config, 'fontBold') ? config['fontBold'] : false;
    var fontItalic = Object.prototype.hasOwnProperty.call(config, 'fontItalic') ? config['fontItalic'] : false;
    var textAlign = Object.prototype.hasOwnProperty.call(config, 'textAlign') ? config['textAlign'] : 'left';
    var borderThickness = Object.prototype.hasOwnProperty.call(config, 'borderThickness') ? config['borderThickness'] : 4;
    var borderColor = Object.prototype.hasOwnProperty.call(config, 'borderColor') ? config['borderColor'] : 'rgba(0, 0, 0, 1)';
    var borderRadius = Object.prototype.hasOwnProperty.call(config, 'borderRadius') ? config['borderRadius'] : 0;
    var backgroundColor = Object.prototype.hasOwnProperty.call(config, 'backgroundColor') ? config['backgroundColor'] : 'rgba(255, 255, 255, 1)';

    if(!ruler) {
      ruler = document.createElement('canvas').getContext('2d');
    }
    ruler.font = (fontBold ?  'Bold ' : '') + (fontItalic ? 'Italic ' : '') + fontSize + 'px ' + fontFace;

    var textLines = text.split('\n');
    var textWidth = 0;
    // canvas width shall be based on the longest width of text lines
    textLines.forEach(function(line) {
      var metrics = ruler.measureText(line);
      textWidth = metrics.width > textWidth ? metrics.width : textWidth;  
    });
    // 1.4 is extra height factor for text below baseline: g,j,p,q.
    var textHeight = fontSize * 1.4 * textLines.length;
    
    // texture canvas
    if(!canvas) {
      canvas = document.createElement('canvas');
    }
    // canvas.width = _ceilPow2(textWidth);
    // canvas.height = _ceilPow2(textHeight);
    canvas.width = textWidth + 2 * borderThickness;
    canvas.height = textHeight + 2 * borderThickness;
    var context = canvas.getContext('2d');
  
    // draw background
    context.font = ruler.font;
    context.fillStyle = backgroundColor;
    context.strokeStyle = borderColor;
    context.lineWidth = borderThickness;
    _roundRect(context, borderThickness / 2, borderThickness / 2, textWidth + borderThickness, textHeight + borderThickness - 1, borderRadius);
    
    // draw text
    context.fillStyle = fontColor;
    context.textAlign = textAlign;
    var fillTextX = {
      left: borderThickness,
      start: borderThickness,
      center: textWidth / 2 + borderThickness,
      right: textWidth + borderThickness,
      end: textWidth + borderThickness
    };
    var curY = fontSize + borderThickness
    textLines.forEach(function(line) {
      context.fillText(line, fillTextX[textAlign], curY);
      curY += fontSize * 1.4;
    })
    
    // generate sprite
    var texture = new THREE.Texture(canvas);
    texture.needsUpdate = true;
    // var spriteMaterial = new THREE.SpriteMaterial({ 
    //   map: texture,
    //   transparent: true,
    //   sizeAttenuation: false
    // });
    // texture.warpS = texture.warpT = THREE.RepeatWrapping;
    var scale = new THREE.Vector2(canvas.width / 10, canvas.height / 10);
    var spriteMaterial = Shader.createTagMaterial(scale, texture, renderedIdValue);
    // set plane geometry and sprite
    if(!sprite) {
      var geometry = new THREE.PlaneGeometry(1, 1);
      sprite = new THREE.Mesh(geometry);
      // https://stackoverflow.com/questions/26806768/three-js-under-what-conditions-are-three-lines-frustum-culled
      sprite.frustumCulled = false; // to enable picking after shape has changed
    }
    // else {
    //   geometry = sprite.geometry;
    // }
    // geometry.vertices[ 0 ].set( -canvas.width / 20, canvas.height / 20, 0 );
    // geometry.vertices[ 1 ].set( canvas.width / 20, canvas.height / 20, 0 );
    // geometry.vertices[ 2 ].set( -canvas.width / 20, -canvas.height / 20, 0 );
    // geometry.vertices[ 3 ].set( canvas.width / 20, -canvas.height / 20, 0 );
    // geometry.verticesNeedUpdate = true;
    sprite.material = spriteMaterial;
    sprite.material.transparent = true;
    sprite.updateMorphTargets();
    
    return {
      sprite: sprite,
      ruler: ruler,
      canvas: canvas
    };
  
    // ceil the input number to the nearest powers of 2
    // function _ceilPow2(num) {
    //   var i = 0;
    //   while (num > Math.pow(2, i)) { 
    //     i++;
    //   }
    //   return Math.pow(2, i);
    // }
  
    // draw round rect
    function _roundRect(ctx, x, y, w, h, r) {
      ctx.beginPath();
      ctx.moveTo(x + r, y);
      ctx.lineTo(x + w - r, y);
      ctx.quadraticCurveTo(x + w, y, x + w, y + r);
      ctx.lineTo(x + w, y + h - r);
      ctx.quadraticCurveTo(x + w, y + h, x + w - r, y + h);
      ctx.lineTo(x + r, y + h);
      ctx.quadraticCurveTo(x, y + h, x, y + h - r);
      ctx.lineTo(x, y + r);
      ctx.quadraticCurveTo(x, y, x + r, y);
      ctx.closePath();
      ctx.fill();
      ctx.stroke();
    }
  }

  
export { generateTextSprite };