<template>
    <div v-show="!objLoaded" class="loading-screen" :class="{'fade-out': loadingScreenFadeout}">
        <div class="row1">{{$t("m.i.loadingScreenName")}}</div>
        <div class="row2">
            <!-- <div v-if="showBtnContainer" class="btn-container" :class="{'visible-container': !objLoading}">
                <v-btn @click="loadCanvas('/viewer')">{{$t("m.i.loadingScreenLoadBtnViewer")}}</v-btn>
                <v-btn @click="loadCanvas('/editor')">{{$t("m.i.loadingScreenLoadBtnEditor")}}</v-btn>
            </div> -->
            <div class="loader-container" :class="{'visible-container': objLoading}">
                <div class="loader-label">
                    {{loadingPhases[$store.state.loadingPhase]}}<br/>{{$store.getters.loadingPercentage}}
                </div>
                <div class="loader" :style="{'width': $store.getters.loadingPercentage}"></div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            objLoading: false,
            objLoaded: false,
            loadingScreenFadeout: false,
            showBtnContainer: true,
            loadingPhases: [
                this.$t("m.i.loadingPhaseDownloadZip"),
                this.$t("m.i.loadingPhaseLoadObj")
            ]
        }
    },
    mounted() {
        this.loadCanvas('/editor')
    },
    methods: {
        resetLoadingScreen() {
            this.objLoading = false;
            this.objLoaded = false;
            this.loadingScreenFadeout = false;
            this.showBtnContainer = true;
        },
        startLoading() {
            this.showBtnContainer = false;
            this.objLoading = true;
        },
        loadCanvas(val) {
            // val: route
            this.objLoading = true;
            setTimeout(() => {
                this.showBtnContainer = false;
                this.$emit('loadCanvas', val);
            }, 500);
        },
        fadeIn() {
            this.objLoading = true;
            this.loadingScreenFadeout = false;
            this.objLoaded = false;
        },
        fadeOut() {
            setTimeout(() => {
                this.loadingScreenFadeout = true;
                setTimeout(() => {
                    this.objLoaded = true;
                }, 500)
            }, 500);
        }
    }
}
</script>

<style scoped>
.row1 {
    font-size: 28pt;
    font-family: Arial, Helvetica, sans-serif;
    font-family: "Microsoft YaHei",微软雅黑,"MicrosoftJhengHei",华文细黑,STHeiti,MingLiu;
    margin-bottom: 100px;
    color: #ffffff;
}
.row2 {
    height: 200px;
}
/* loading */
.loading-screen {
	position: absolute;
	z-index: 200;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
	background-color: #000000;
    opacity: 1;
    transition: 500ms;
}
.loading-screen.fade-out {
    opacity: 0;
}
.btn-container {
	opacity: 0;
    transition: 500ms;
}
.btn-container >>> button {
    margin: 0 10px;
}
.visible-container {
    opacity: 1;
}
.loader-container {
    display: block;
    position: relative;
    width: 200px;
    height: 150px;
}
.loader-label {
    width:100%;
    text-align: center;
    color: white;
    font-size: 16pt;
}
.loader {
    display: block;
    position: absolute;
    height: 5px;
    background-color: #ffffff;
}
</style>