<template>
    <v-row>
        <v-col cols="8">{{name}}</v-col>
        <v-col cols="4">
            <v-btn
            tile
            class="small-btn-2"
            :disabled="disabled"
            :color="value"
            @click.stop="openColorController"></v-btn>
        </v-col>
        
        <!-- color controller -->
        <v-dialog
            v-if="colorPanelOpen"
            v-model="colorPanelOpen"
            overlay-opacity="0"
            width="min-content"
            >
                <v-color-picker dark
                :value="value"
                @update:color="colorControllerChanged"
                ></v-color-picker>
        </v-dialog>
    </v-row>
</template>

<script>
export default {
    props: {
        name: String,
        value: String,
        disabled: {
            type: Boolean,
            default: false,
        },
        alphaEnabled: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            colorPanelOpen: false
        };
    },
    methods: {
        openColorController() {
            this.colorPanelOpen = true;
        },
        colorControllerChanged(val) {
            this.$emit('input', this.alphaEnabled ? val.hexa : val.hex)
        }
    }
}
</script>

<style scoped>
.small-btn-2 {
    height: 100% !important;
    min-width: 100% !important;
}
</style>