<template>
    <v-row no-gutters class="justify-space-around">
        <v-col class="nf-btn-var-title">{{title}}</v-col>
        <v-col class="nf-btn-var-btn">
            <v-btn
            class="nf-btn"
            tile
            @click="$emit('click')" :color="color">
                <slot>{{name}}</slot>
            </v-btn>
        </v-col>
    </v-row>
</template>

<script>
export default {
    props: {
        title: {
            type: String,
            default: "title"
        },
        name: {
            type: String,
            default: "name"
        },
        color: {
            type: String,
            default: "grey"
        }
    }
}
</script>

<style scoped>
.nf-btn {
    margin: 6px;
    text-transform: inherit;
}
.nf-btn-var-title {
    display: flex;
    align-items: center;
}
.nf-btn-var-btn {
    display: flex;
    justify-content: center;
}
</style>