<template>
    <v-container class="right-panel-container">
        <nl-title :title="$t('m.i.scene')"></nl-title>
        <v-expansion-panels style="height: 0" class="right-panel" tile accordion multiple v-model="openedPanels">
            <v-expansion-panel v-if="isViewer">
                <v-expansion-panel-header>{{$t('m.i.file')}}</v-expansion-panel-header>
                <v-expansion-panel-content>
                    <nf-buttons :btns="fileBtnGroup"
                    @reload="reload"
                    @downloadConfig="downloadConfig"
                    @useConfig="useConfig"
                    @saveScreenshot="saveScreenshot"
                    ></nf-buttons>
                    <input
                    type="file"
                    ref="json-file-input"
                    accept=".json"
                    style="visibility:hidden;position:absolute"
                    @change="fileInputChange"
                    />
                </v-expansion-panel-content>
            </v-expansion-panel>
            <v-expansion-panel>
                <v-expansion-panel-header>{{$t('m.i.sceneSettings')}}</v-expansion-panel-header>
                <v-expansion-panel-content>
                    <nf-color :name="$t('m.i.background')" v-model='bgColorVal' @input="bgColorChanged"></nf-color>
                    <nf-buttons :btns="moreOptionsBtnGroup"
                    @openMoreOptionsDialog="openMoreOptionsDialog"
                    ></nf-buttons>
                </v-expansion-panel-content>
            </v-expansion-panel>
        </v-expansion-panels>
        <lights-dialog
        ref="lights-dialog"
        :lights="lights"
        @lightValChanged="lightValChanged"
        @controlsTypeChanged="controlsTypeChanged"></lights-dialog>
    </v-container>
</template>

<script>
import LightsDialog from "../dialogs/LightsDialog";

export default {
    name: 'ScenePanel',
    components: {
        LightsDialog
    },
    props: {
        isViewer: Boolean,
        bgColor: String,
        lights: Array,
        neuronList: Array
    },
    data() {
        return {
            fileBtnGroup: [
                {label: this.$t("m.i.relaodScene"), method: 'reload'},
                // {label: this.$t("m.i.downloadConfig"), method: 'downloadConfig'},
                // {label: this.$t("m.i.useConfig"), method: 'useConfig'},
                {label: this.$t("m.i.saveScreenshot"), method: 'saveScreenshot'}
            ],
            moreOptionsBtnGroup: [
                {label: this.$t("m.i.moreOptions"), method: 'openMoreOptionsDialog'},
            ],
            openedPanels: [0, 1],
            bgColorVal: '#ffff00',
            model: [],
        }
    },
    created() {
        this.bgColorVal = this.bgColor;
    },
    methods: {
        reload() {
            var doReload = window.confirm(this.$t("m.a.qDoReloadScene"));
            if(doReload) {
                this.$emit("reload");
            }
        },
        downloadConfig() {
            this.$emit("downloadConfig");
        },
        saveScreenshot() {
            this.$emit("saveScreenshot");
        },
        bgColorChanged() {
            this.$emit('bgColorChanged', this.bgColorVal);
        },
        lightValChanged(index) {
            var tempLight = this.lights[index];
            
            tempLight.outColor.x = tempLight.color.r;
            tempLight.outColor.y = tempLight.color.g;
            tempLight.outColor.z = tempLight.color.b;
            tempLight.outColor =  tempLight.outColor.multiplyScalar(tempLight.strength);
            var paramName = "light" + (index+1) + "Color"
            this.neuronList.forEach(item => {
                let colorCol = item.neuron.material.uniforms[paramName].value;
                colorCol.x = tempLight.outColor.x;
                colorCol.y = tempLight.outColor.y;
                colorCol.z = tempLight.outColor.z;
            })
            this.$emit('renderOnce');
        },
        useConfig() {
            var fileInput = this.$refs["json-file-input"];
            fileInput.click();
        },
        /**
         * read json config file from input
         */
        fileInputChange(e) {
            var self = this;
            var reader = new FileReader();
            reader.readAsText(e.target.files[0], "UTF-8");
            e.target.value = '';
            reader.onload = function (e) {
                var fileString = e.target.result;
                try {
                    var configObj = JSON.parse(fileString);
                    if(configObj.fileList === undefined) {
                        throw "err";
                    }
                    self.$emit("loadCanvas", configObj);
                }
                catch(err) {
                    self.$store.commit("alert", {
                        content: this.$t("m.a.jsonFileInvalid")
                    })
                }
            }
        },
        /**
         * opens "more options" dialog to adjust light settings
         */
        openMoreOptionsDialog() {
            this.$refs["lights-dialog"].open();
        },
        controlsTypeChanged(v) {
            this.$emit("controlsTypeChanged", v);
        }
    },
}
</script>

<style scoped>
.light-title-row {
    font-weight: bold;
}
</style>