<template>
    <v-row>
        <v-col>
            <v-checkbox
            class="ma-0 pa-0"
            light
            :label="displayedName"
            :disabled="disabled"
            :value="value"
            @change="change"
            hide-details></v-checkbox>
        </v-col>
    </v-row>
</template>

<script>
export default {
    props: {
        label: {
            type: [Array, String],
            default () {
                return []
            }
        },
        value: Boolean,
        disabled: {
            type: Boolean,
            default: false,
        }
    },
    computed: {
        displayedName() {
            if (this.label.constructor == String) {
                return this.label;
            }
            return this.label[this.value + 0];
        }
    },
    methods: {
        change(val) {
            this.$emit("input", val);
        }
    }
}
</script>

<style scoped>
.small-btn-2 {
    height: 20px !important;
}
</style>