<template>
    <v-container class="right-panel-container">
        <nl-title :title="$t('m.i.draw')"></nl-title>
        <v-expansion-panels class="right-panel" tile accordion multiple v-model="openedPanels">
            <v-expansion-panel>
                <v-expansion-panel-header>{{$t('m.i.tagOperations')}}</v-expansion-panel-header>
                <v-expansion-panel-content>
                    <nf-buttons
                    :btns="tagBtns"
                    @createTag="createTag"
                    @deleteTag="deleteTag"
                    @relocateTag="relocateTag"
                    @toggleSelectTag="toggleSelectTag"></nf-buttons>
                </v-expansion-panel-content>
            </v-expansion-panel>
            <v-expansion-panel>
                <v-expansion-panel-header>{{$t('m.i.tagList')}}</v-expansion-panel-header>
                <v-expansion-panel-content class="list-content">
                    <v-row no-gutters>
                        <tag-table
                        ref="tagTable"
                        v-model="selectedTags"
                        :tagList="tagList"
                        @focusOnItem="focusOnItem"
                        @renderOnce="renderOnce"
                        @openModifyTagDialog="openModifyTagDialog"
                        ></tag-table>
                    </v-row>
                </v-expansion-panel-content>
            </v-expansion-panel>
            <v-expansion-panel>
                <v-expansion-panel-header>{{$t('m.i.tagBatchOperations')}}</v-expansion-panel-header>
                <v-expansion-panel-content>
                    <nf-buttons
                    :btns="batchBtns"
                    @batchSetTag="batchSetTag"
                    @batchDeleteTag="batchDeleteTag"></nf-buttons>
                </v-expansion-panel-content>
            </v-expansion-panel>
        </v-expansion-panels>
        <new-tag-dialog
        ref="newTagDialog"
        v-model="newTagDialogOpen"
        :bgColor="bgColor"
        @newTagDialogClosed="newTagDialogClosed"></new-tag-dialog>
    </v-container>
</template>

<script>
import * as THREE from "three";
import NewTagDialog from "../dialogs/NewTagDialog";
import TagTable from '../TagTable';
import { generateTextSprite } from '../js/threejs-text-sprite';

export default {
    components: {
        NewTagDialog,
        TagTable
    },
    props: {
        bgColor: {
            type: String,
            default: "#000000"
        },
        tagList: {
            type: Array,
            default: () => []
        },
        tagActionIndex: {
            type: Number,
            default: 0
        },
        controls: {
            default: null,
            type: Object
        }
    },
    data() {
        return {
            openedPanels: [0, 1, 2],
            
            tagBtns: [
                {
                    label: this.$t("m.i.addTag"),
                    method: "createTag"
                },
                {
                    label: this.$t("m.i.deleteTag"),
                    method: "deleteTag"
                },
                {
                    label: this.$t("m.i.relocateTag"),
                    method: "relocateTag"
                },
                {
                    label: this.$t("m.i.toggleSelectTag"),
                    method: "toggleSelectTag"
                }
            ],
            batchBtns: [
                {
                    label: this.$t("m.i.batchSetTag"),
                    method: "batchSetTag"
                },
                {
                    label: this.$t("m.i.batchDeleteTag"),
                    method: "batchDeleteTag"
                }
            ],
            tagIsBeingModified: false,
            tagIsBeingBatchModified: false,
            newTagDialogOpen: false,
            tagPositionInfo: undefined,
            selectedTags: [],
        }
    },
    mounted() {
        this.newTagDialog = this.$refs["newTagDialog"];
    },
    methods: {
        createTag() {
            // tag creation procedures:
            // 1. canvas enables selection mode
            // 2. click to set position and neuron; calls tagPositionSet
            // 3. (if selection is valid) opens NewTagDialog, passing neuron name
            // 4. (if dialog closes on confirm) dialog returns a sprite with an UNSET position
            // 5. set sprite position
            if(this.tagActionIndex !== 1) {
                this.$emit("startCreatingTag");
            }
            else {
                this.$emit("endCreatingTag");
            }
        },
        /**
         * step 2
         */
        tagPositionSet(info, idValue) {
            this.tagPositionInfo = info;
            if(!info[0] || !info[0].neuron) {
                // didn't pick any neuron, or picked tag instead of neuron
                this.$store.commit("alert", {
                    content: this.$t("m.a.neuronNotSelected"),
                });
                return;
            }
            // step 3
            this.newTagDialogOpen = true;
            this.newTagDialog.setValues(undefined, info[0].neuron.name, idValue);
            // create new tag at the specified position, in NewTagDialog
        },
        /**
         * step 4
         * @param val a sprite if dialog is closed on confirm; otherwise, null
         */
        newTagDialogClosed(tagInfo) {
            if(this.tagIsBeingBatchModified) {
                // batch modifying tag done
                this.tagIsBeingBatchModified = false;
                this.selectedTags.forEach(originalTagInfo => {
                    var originalTagIsSelected = originalTagInfo.sprite.material.uniforms.isSelected.value;
                    var generatedSpriteInfo = generateTextSprite(
                        originalTagInfo.text,
                        {
                            fontSize: tagInfo.fontSize,
                            fontColor: tagInfo.fontColor,
                            fontBold: tagInfo.fontBold,
                            fontItalic: tagInfo.fontItalic,
                            textAlign: tagInfo.textAlign,
                            borderThickness: tagInfo.borderThickness,
                            borderColor: tagInfo.hasBorder ? tagInfo.borderColor:'#00000000',
                            borderRadius: tagInfo.borderRadius,
                            backgroundColor: tagInfo.hasBackground ? tagInfo.backgroundColor:'#00000000'
                        },
                        originalTagInfo.idValue / 1000,
                        originalTagInfo.ruler,
                        originalTagInfo.canvas,
                        originalTagInfo.sprite
                    );
                    var sprite = generatedSpriteInfo.sprite;
                    var ruler = generatedSpriteInfo.ruler;
                    var canvas = generatedSpriteInfo.canvas;
                    
                    originalTagInfo.sprite = sprite;
                    // originalTagInfo.text = tagInfo.text;
                    originalTagInfo.fontItalic = tagInfo.fontItalic;
                    originalTagInfo.fontBold = tagInfo.fontBold;
                    originalTagInfo.fontSize = tagInfo.fontSize;
                    originalTagInfo.textAlign = tagInfo.textAlign;
                    originalTagInfo.fontColor = tagInfo.fontColor;
                    originalTagInfo.hasBorder = tagInfo.hasBorder;
                    originalTagInfo.borderColor = tagInfo.borderColor;
                    originalTagInfo.borderRadius = tagInfo.borderRadius;
                    originalTagInfo.borderThickness = tagInfo.borderThickness;
                    originalTagInfo.hasBackground = tagInfo.hasBackground;
                    originalTagInfo.backgroundColor = tagInfo.backgroundColor;
                    // originalTagInfo.idValue = tagInfo.idValue;
                    originalTagInfo.ruler = ruler;
                    originalTagInfo.canvas = canvas;
                    originalTagInfo.sprite.material.uniforms.isSelected.value = originalTagIsSelected;
                    this.renderOnce();
                })
            }
            else if(this.tagIsBeingModified) {
                // modifying existing tag done
                if(tagInfo && tagInfo.sprite) {
                    // sets position
                    this.$emit("renderOnce");
                }
                this.tagIsBeingModified = false;
            }
            else {
                // creating new tag done
                if(tagInfo && tagInfo.sprite) {
                    // sets position
                    tagInfo.sprite.position.set(this.tagPositionInfo[1], this.tagPositionInfo[2], this.tagPositionInfo[3]);
                    tagInfo.position = [this.tagPositionInfo[1], this.tagPositionInfo[2], this.tagPositionInfo[3]];
                }
                this.$emit("endCreatingTag", tagInfo);
            }
        },
        /**
         * delete a tag
         */
        deleteTag() {
            if(this.tagActionIndex != 2) {
                this.$emit("startDeletingTag");
            }
            else {
                this.$emit("cancelDeletingTag");
            }
        },
        renderOnce() {
            this.$emit("renderOnce");
        },
        openModifyTagDialog(tagInfo) {
            this.newTagDialogOpen = true;
            this.tagIsBeingModified = true;
            this.newTagDialog.setValues(tagInfo, tagInfo.text, tagInfo.idValue);
        },
        relocateTag() {
            if(this.tagActionIndex != 3) {
                this.$emit("startRelocatingTag");
            }
            else {
                this.$emit("cancelRelocatingTag");
            }
        },
        toggleSelectTag() {
            if(this.tagActionIndex != 4) {
                this.$emit("startToggleSelectTag");
            }
            else {
                this.$emit("cancelToggleSelectTag");
            }
        },
        toggleSelectTagChange(tagToToggleSelect) {
            var tagIndex = this.selectedTags.indexOf(tagToToggleSelect);
            if(tagIndex < 0) {
                // add tag to selected array
                this.selectedTags.push(tagToToggleSelect);
                tagToToggleSelect.sprite.material.uniforms.isSelected.value = true;
            }
            else {
                // remove tag from selected array
                this.selectedTags.splice(tagIndex, 1);
                tagToToggleSelect.sprite.material.uniforms.isSelected.value = false;
            }
            this.$refs["tagTable"].forceRefreshSelectedTags();
            this.renderOnce();
        },
        focusOnItem(tagInfo) {
                        // this.camera.position.set(boundingSphere.center.x + boundingSphere.radius,
            //                             boundingSphere.center.y + boundingSphere.radius,
            //                             boundingSphere.center.z + boundingSphere.radius);
            this.controls.target.fromArray(tagInfo.position);
            this.controls.update();
            this.renderOnce();
        },
        batchSetTag() {
            if(this.selectedTags.length <= 0) {
                this.$store.commit("alert", {
                    content: this.$t("m.a.tagNotSelected"),
                });
                return;
            }
            this.tagIsBeingBatchModified = true;
            this.newTagDialogOpen = true;
            // is operating on multiple tags
            this.newTagDialog.isOperatingOnSingleTag = false;
            this.newTagDialog.setValues(undefined, this.$t("m.i.exampleText"), 0);
            // this.selectedNeurons.forEach((n) => {
            //     n.neuron.visible = val;
            // })
            this.renderOnce();
        },
        batchDeleteTag() {
            if(this.selectedTags.length <= 0) {
                this.$store.commit("alert", {
                    content: this.$t("m.a.tagNotSelected"),
                });
                return;
            }
            // having selected the tags about to be removed
            var flag = confirm(this.$t("m.a.qDoBatchRemoveTag"));
            if(flag) {
                this.selectedTags.forEach(tagInfo => {
                    this.$emit("deleteTag", tagInfo);
                })
                this.selectedTags.length = 0;
                this.$refs["tagTable"].forceRefreshSelectedTags();
                this.renderOnce();
            }
            // this.selectedNeurons.forEach((n) => {
            //     n.neuron.visible = val;
            // })
            this.renderOnce();
        }
    },
    beforeDestroy() {
        // reset tag action to 0
        this.$emit("drawPanelEnded");
    },
    watch: {
        tagActionIndex(val) {
            this.tagBtns[0].label = this.$t("m.i.addTag");
            this.tagBtns[1].label = this.$t("m.i.deleteTag");
            this.tagBtns[2].label = this.$t("m.i.relocateTag");
            this.tagBtns[3].label = this.$t("m.i.toggleSelectTag");
            switch(val) {
                case 1:
                    this.tagBtns[0].label = this.$t("m.i.cancelAddTag");
                    break;
                case 2:
                    this.tagBtns[1].label = this.$t("m.i.cancelDeleteTag");
                    break;
                case 3:
                    this.tagBtns[2].label = this.$t("m.i.cancelRelocateTag");
                    break;
                case 4:
                    this.tagBtns[3].label = this.$t("m.i.cancelToggleSelectTag");
                    break;
            }
        }
    }
}
</script>

<style scoped>
 .list-content >>> .v-expansion-panel-content__wrap {
     padding: 0;
 }
</style>