import Vue from 'vue'
import Vuex from 'vuex'
import { TrackballControls } from 'three/examples/jsm/controls/TrackballControls';

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        loadingPhase: 0,

        loadingProgressComponent: undefined,
        loadingProgress: 0,

        controls: undefined,

        // panel
        panelOpen: false,
        panelIndex: 0,

        alertOpenMethod: undefined,
        isMobile: false,
    },
    getters: {
        loadingPercentage: state => {
            return Math.round(state.loadingProgress * 100) + '%';
        },
        controlsType: state => {
            if(!state.controls) {
                return 0;
            }
            if(state.controls instanceof TrackballControls) {
                return 1;
            }
            return 0;
        }
    },
    mutations: {
        setControls(state, controls) {
            state.controls = controls;
        },
        setLoadingProgressComponent(state, component) {
            state.loadingProgressComponent = component;
        },
        resetLoadingProgressComponent(state, component) {
            if(state.loadingProgressComponent) {
                state.loadingProgressComponent.resetLoadingScreen();
            }
        },
        loadingProgressChanged(state, progress) {
            state.loadingProgressComponent.startLoading();
            state.loadingProgress = progress;
        },
        loadingScreenFadeIn(state) {
            state.loadingProgressComponent.fadeIn();
        },
        loadingScreenFadeOut(state) {
            state.loadingProgressComponent.fadeOut();
        },
        setPanelStat (state, stat) {
            if(stat.open != undefined)
                state.panelOpen = stat.open;
            if(stat.index != undefined)
                state.panelIndex = stat.index;
        },
        setAlertOpenMethod(state, method) {
            state.alertOpenMethod = method;
        },
        alert(state, stat) {
            state.alertOpenMethod(stat);
        },
        setIsMobile(state) {
            state.isMobile = /Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent);
        }
    }
})