import VueRouter from 'vue-router'
// import MyCanvas from './components/MyCanvas.vue'
// import MyCanvasEditor from './components/MyCanvasEditor.vue'
import store from './store/store'

const router = new VueRouter({
    mode: 'history',
    routes: [
        { path: '/', component: null },
        { path: '/viewer', component: resolve => require(['@/components/MyCanvas.vue'], resolve) },
        { path: '/editor', component: resolve => require(['@/components/MyCanvasEditor.vue'], resolve)  }
    ]
})

router.beforeEach((to, from, next) => {
    if(to.path == '/' && from.path != '/') {
        // from canvas back to loading screen
        store.commit("resetLoadingProgressComponent");
    }
    next()
})

export default router;