<template>
    <v-container class="right-panel-container">
        <nl-title :title="$t('m.i.neuron')"></nl-title>
        <v-expansion-panels class="right-panel" tile accordion multiple v-model="openedPanels">
            <v-expansion-panel>
                <v-expansion-panel-header>{{$t('m.i.neuronList')}}</v-expansion-panel-header>
                <v-expansion-panel-content class="list-content">
                    <v-row no-gutters>
                        <neuron-list-table
                        ref="neuronListTable"
                        :router="router"
                        :neuronList="neuronList"
                        v-model="selectedNeurons"
                        @renderOnce="valueChanged"
                        @openMaterialDialog="openMaterialDialog"
                        @focusOnItem="focusOnItem"
                        @groupNameChanged="groupNameChanged">
                        </neuron-list-table>
                    </v-row>
                </v-expansion-panel-content>
            </v-expansion-panel>
            <v-expansion-panel>
                <v-expansion-panel-header>{{$t('m.i.batchOperations')}}</v-expansion-panel-header>
                <v-expansion-panel-content>
                    <nf-buttons :btns="batchOperationBtnGroup1"
                    @batchSetVisible="batchSetVisibility(true)"
                    @batchSetInvisible="batchSetVisibility(false)"></nf-buttons>
                    <nf-buttons style="margin-top: 0" :btns="batchOperationBtnGroup2"
                    @batchSetColor="batchSetColor"
                    @batchSetMaterial="batchSetMaterial"
                    @groupDialogOpen="groupDialogOpen"></nf-buttons>
                </v-expansion-panel-content>
            </v-expansion-panel>
        </v-expansion-panels>
        <material-dialog
        v-model="materialDialogOpen"
        :neuron="selectedNeuron"
        @materialChanged="valueChanged"></material-dialog>
        
        <!-- batch color dialog -->
        <v-dialog
            v-if="batchColorPanelOpen"
            v-model="batchColorPanelOpen"
            overlay-opacity="0"
            width="min-content"
            >
                <v-color-picker dark
                v-model="batchColor"
                @update:color="batchColorChanged"
                ></v-color-picker>
        </v-dialog>

        <!-- batch material dialog -->
        <material-dialog
        ref="batchMaterialDialog"
        v-model="batchMaterialDialogOpen"
        @materialChanged="batchValueChanged"></material-dialog>

        <!-- batch group dialog -->
        <group-dialog
        ref="batchGroupDialog"
        v-model="selectedNeurons"
        :groupList="groupList"
        :neuronList="neuronList"
        @input="groupDialogClosed"></group-dialog>
    </v-container>
</template>

<script>
import * as THREE from 'three';
import MaterialDialog from '../dialogs/MaterialDialog.vue';
import GroupDialog from '../dialogs/GroupDialog.vue';
import NeuronListTable from '../NeuronListTable.vue';
import * as Shader from '../shaders';
export default {
  components: { NeuronListTable, MaterialDialog, GroupDialog },
    props: {
        router: {
            type: String,
            default: "viewer"
        },
        camera: {
            default: null,
            type: Object
        },
        controls: {
            default: null,
            type: Object
        },
        neuronList: {
            default: () => [],
            type: Array,
        },
        groupList: {
            default: () => [],
            type: Array,
        }
    },
    data() {
        return {
            openedPanels: [0, 1],
            selectedNeurons: [],
            // btns
            batchOperationBtnGroup1: [
                {label: this.$t("m.i.visible"), method: 'batchSetVisible'},
                {label: this.$t("m.i.invisible"), method: 'batchSetInvisible'},
            ],
            batchOperationBtnGroup2: [
                {label: this.$t("m.i.colorSetting"), method: 'batchSetColor'},
                {label: this.$t("m.i.materialSetting"), method: 'batchSetMaterial'},
            ],
            materialDialogOpen: false,
            selectedNeuron: null,

            batchColorPanelOpen: false,
            batchColor: "#ff0000",
            
            batchMaterialDialogOpen: false,

            neuronListTable: undefined,
        }
    },
    created() {
        if(this.router == "editor") {
            this.batchOperationBtnGroup2.push({label: this.$t("m.i.groupingSetting"), method: 'groupDialogOpen'});
        }
    },
    mounted() {
        this.neuronListTable = this.$refs["neuronListTable"];
    },
    methods: {
        valueChanged() {
            this.$emit("renderOnce");
        },
        itemMaterialChanged(item) {
            this.$emit("renderOnce");
        },
        openMaterialDialog(neuron) {
            this.materialDialogOpen = true;
            this.selectedNeuron = neuron;
        },
        /**
         * move camera to focus on item
         */
        focusOnItem(item) {
            var boundingSphere = item.neuron.geometry.boundingSphere;
            
            // this.camera.position.set(boundingSphere.center.x + boundingSphere.radius,
            //                             boundingSphere.center.y + boundingSphere.radius,
            //                             boundingSphere.center.z + boundingSphere.radius);
            this.controls.target.set(boundingSphere.center.x,
                                        boundingSphere.center.y,
                                        boundingSphere.center.z);
            this.controls.update();
            this.valueChanged();
        },
        batchSetVisibility(val) {
            if(this.selectedNeurons.length <= 0) {
                this.$store.commit("alert", {
                    content: this.$t("m.a.neuronNotSelected"),
                });
                return;
            }
            this.selectedNeurons.forEach((n) => {
                n.neuron.visible = val;
            })
            this.valueChanged();
        },
        batchSetColor() {
            if(this.selectedNeurons.length) {
                // set batch color to one of the selected neuron's color
                this.batchColor =
                    "#" + this.selectedNeurons[0].neuron.material.uniforms.primaryColor.value.getHexString();
                this.batchColorPanelOpen = true;
            }
            else {
                this.$store.commit("alert", {
                    content: this.$t("m.a.neuronNotSelected"),
                });
                return;
            }
        },
        batchColorChanged(colorVal) {
            this.selectedNeurons.forEach((n) => {
                let neuronCol = n.neuron.material.uniforms.primaryColor.value;
                neuronCol.r = colorVal.rgba.r / 255.0;
                neuronCol.g = colorVal.rgba.g / 255.0;
                neuronCol.b = colorVal.rgba.b / 255.0;
            })
            this.valueChanged();
        },
        batchSetMaterial() {
            if(this.selectedNeurons.length <= 0) {
                this.$store.commit("alert", {
                    content: this.$t("m.a.neuronNotSelected"),
                });
                return;
            }
            var batchMaterialDialog = this.$refs["batchMaterialDialog"];
            var materialTypeRec = new Array(batchMaterialDialog.materialTypes.length);
            this.selectedNeurons.forEach(n => {
                // uses method in batchMaterialDialog
                let typeInt = Math.round(n.neuron.material.uniforms.type.value);
                materialTypeRec[typeInt] = true;
            });
            var trueMatType = -1;
            var trueMatTypeCount = 0;
            materialTypeRec.forEach((rec, i) => {
                if(rec) {
                    trueMatType = i;
                    trueMatTypeCount += 1;
                }
            })
            if(trueMatTypeCount == 1) {
                batchMaterialDialog.selectedMaterial = trueMatType;
            }
            else {
                batchMaterialDialog.selectedMaterial = -1.0;
            }
            this.batchMaterialDialogOpen = true;
        },
        batchValueChanged(e) {
            if(e[1] == 1) {
                // main color changed
                this.selectedNeurons.forEach((n) => {
                    let neuronCol = n.neuron.material.uniforms.primaryColor.value;
                    neuronCol.r = e[0].r;
                    neuronCol.g = e[0].g;
                    neuronCol.b = e[0].b;
                })
            }
            else if(e[1] == 2) {
                // secondary color changed
                this.selectedNeurons.forEach((n) => {
                    let neuronCol = n.neuron.material.uniforms.secondaryColor.value;
                    neuronCol.r = e[0].r;
                    neuronCol.g = e[0].g;
                    neuronCol.b = e[0].b;
                })
            }
            else if(e[1] == 3) {
                // material type changed
                this.selectedNeurons.forEach((n) => {
                    n.neuron.material.uniforms.type.value = e[0];
                })
            }
            else if(e[1] == 4) {
                // wireframe changed
                this.selectedNeurons.forEach((n) => {
                    n.neuron.material.wireframe = e[0];
                })
            }
            else if(e[1] == 5) {
                // shininess changed
                this.selectedNeurons.forEach((n) => {
                    n.neuron.material.uniforms.shininess.value = e[0];
                })
            }
            this.valueChanged();
        },
        groupDialogOpen() {
            this.$refs["batchGroupDialog"].open();
        },
        /**
         * refresh table selections
         */
        groupDialogClosed() {
            this.$nextTick(() => {
                this.valueChanged();
                this.neuronListTable.forceRefreshSelectedNeurons();
            })
        },
        /**
         * used for MyCanvas to toggle selection status when clicked on neuron
         */
        neuronPicked(pickedNeuron) {
            // console.log(pickedNeuron);
            if(!pickedNeuron) {
                // no neuron is picked
                return;
            }
            this.$nextTick(() => {
                var indexOfPickedNeuron = this.selectedNeurons.indexOf(pickedNeuron);
                if(indexOfPickedNeuron < 0) {
                    // picked neuron is not selected; change status and material to selected
                    this.selectedNeurons.push(pickedNeuron);
                    this.neuronListTable.setSelectMaterial(pickedNeuron, true);
                }
                else{
                    // picked neuron is selected; change status and material to not selected
                    this.selectedNeurons.splice(indexOfPickedNeuron, 1);
                    this.neuronListTable.setSelectMaterial(pickedNeuron, false);
                }
                this.neuronListTable.forceRefreshSelectedNeurons();
                this.valueChanged();
            })
        },
        groupNameChanged(vals) {
            let indexOfNewVal = this.groupList.indexOf(vals[0]);
            let indexOfOldVal = this.groupList.indexOf(vals[1]);
            if(indexOfNewVal >= 0) {
                this.groupList.splice(indexOfNewVal, 1);
            }
            else {
                this.groupList[indexOfOldVal] = vals[0];
                // $forceUpdate();
            }
        }
    },
    beforeDestroy() {
        // clears selection material before leaving the panel
        this.neuronList.forEach(item => {
            this.neuronListTable.setSelectMaterial(item, false);
        })
    }
}
</script>

<style scoped>
.neuron-table >>> .v-data-footer__select {
     display: none;
 }
 .list-content >>> .v-expansion-panel-content__wrap {
     padding: 0;
 }
</style>