<template>
    <div class="canvas-container">

        <!-- alert dialog -->
        <alert-snackbar></alert-snackbar>
        <v-card
        class="pointer-tag pb-2 flex-column align-center"
        color="#80808080"
        v-if="mouseHintShow"
        >
            <v-flex class="pa-2" v-html="mouseHint"></v-flex>
            <v-btn
            tile
            @click="mouseHintShow=false">{{$t("m.i.gotIt")}}</v-btn>
        </v-card>
        <!-- toggle buttons -->
        

        <!-- <loading-screen
        ref="loading-screen"
        :loadingProgress="loadingProgress"
        @loadObj="loadObj"></loading-screen> -->

        <div class="canvas-box" ref="canvas-box">
            <canvas
            ref="main-canvas"
            id="main-canvas"
            :class="{
                // change cursor to pointer on some conditions
                'canvas--pointer-on': canvasPointerIsOn,
            }"
            @mousedown="onCanvasMouseDown"
            @mouseup="onCanvasClickedOrTouched"
            @dblclick="onCanvasDblClick"
            @touchstart="onCanvasTouchStart"
            @touchend="onCanvasTouchEnd"
            ></canvas>
        </div>
        <!-- <camera-joystick></camera-joystick> -->
        <side-bar ref="side-bar" class="editor-right-bar" :sceneIsSet="sceneIsSet">
            <template v-slot:file>
                <file-panel @loadObj="loadObj"
                @saveConfigJson="saveConfigJson"
                ></file-panel>
            </template>
            <template v-slot:scene>
                <scene-panel
                ref="scenePanel"
                :isViewer="false"
                :bgColor="bgColor"
                @bgColorChanged="bgColorChanged"
                :lights="lights"
                :neuronList="neuronList"
                @renderOnce="render"
                @controlsTypeChanged="controlsTypeChanged($event)"></scene-panel>
            </template>
            <template v-slot:draw>
                <draw-panel
                ref="drawPanel"
                :bgColor="bgColor"
                :tagList="tagList"
                :tagActionIndex="tagActionIndex"
                :controls="controls"
                @startCreatingTag="startCreatingTag"
                @endCreatingTag="endCreatingTag"
                @startDeletingTag="startDeletingTag"
                @cancelDeletingTag="cancelDeletingTag"
                @startRelocatingTag="startRelocatingTag"
                @cancelRelocatingTag="cancelRelocatingTag"
                @startToggleSelectTag="startToggleSelectTag"
                @cancelToggleSelectTag="cancelToggleSelectTag"
                @renderOnce="render"
                @drawPanelEnded="drawPanelEnded"
                @deleteTag="deleteTag"
                ></draw-panel>
            </template>
            <template v-slot:neurons>
                <neuron-panel
                ref="neuronPanel"
                router="editor"
                :neurons="loadedObj"
                :camera="camera"
                :controls="controls"
                :neuronList="neuronList"
                :groupList="groupList"
                @renderOnce="render"></neuron-panel>
            </template>
            <!-- <template v-slot:template>
                <template-panel></template-panel>
            </template> -->
        </side-bar>
        <!-- <copyright-bar></copyright-bar> -->
        <!-- opens material dialog on right click -->
        <material-dialog
        v-model="rightClickMaterialDialogOpen"
        :neuron="rightClickSelectedNeuron"
        @materialChanged="render"></material-dialog>
    </div>
</template>

<script>
import * as THREE from 'three';
import { TrackballControls } from 'three/examples/jsm/controls/TrackballControls';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import {
    OBJLoader2
} from 'three/examples/jsm/loaders/OBJLoader2';
// import LoadingScreen from './LoadingScreen';
import SideBar from './SideBar';
import FilePanel from './panels/FilePanel';
import ScenePanel from './panels/ScenePanel';
import DrawPanel from './panels/DrawPanel';
import NeuronPanel from './panels/NeuronPanel';
// import TemplatePanel from './panels/TemplatePanel';
import AlertSnackbar from './dialogs/AlertSnackbar';
// import CameraJoystick from './CameraJoystick';
// import CopyrightBar from './CopyrightBar';
import MaterialDialog from './dialogs/MaterialDialog';

import CommonMethods from './js/Common';
import * as Shader from './shaders';
import { generateTextSprite } from './js/threejs-text-sprite';
import { download } from './js/FileSaver';
import { Vector3 } from 'three';
import axios from "axios";
import JSZip from 'jszip';
import JSZipUtils from 'jszip-utils';

export default {
    name: 'MyCanvas',
    components: {
        // LoadingScreen,
        SideBar,
        FilePanel,
        ScenePanel,
        DrawPanel,
        NeuronPanel,
        // TemplatePanel,
        AlertSnackbar,
        // CameraJoystick,
        // CopyrightBar,
        MaterialDialog
    },
    data: function() {
        return {
            modelPath: undefined,
            // scene & camera
            fov: 30,
            near: 0.1,//最小范围
            far: 10000,//最大范围
            
            canvasWidth: 600,
            canvasHeight: 400,

            canvas: undefined,
            canvasBox: undefined,
            sideBar: undefined,
            camera: undefined,
            scene: undefined,
            renderer: undefined,
            controls: undefined,

            three: undefined,

            // scene
            bgColor: '#000000',
            blackColor: '#000000',
            // true if the scene is loaded and could be rendered
            sceneIsSet: false,
            // to prevent scene, renderers and camera to mount repeatedly
            sceneIsFirstSet: false,
            sceneIsOn: true,
            panelsOpen: false,

            // light
            lights: [
                {
                    color: undefined,
                    strength: 1,
                    outColor: undefined
                },
                {
                    color: undefined,
                    strength: 0.5,
                    direction: new Vector3(1.0, 0.0, 0.0),
                    outColor: undefined
                },
                {
                    color: undefined,
                    strength: 0.5,
                    direction: new Vector3(-1.0, 0.0, 0.0),
                    outColor: undefined
                }
            ],


            // obj
            loadedObj: undefined,
            neuronList: [],
            groupList: [],
            defaultGroupText: this.$t("m.i.defaultGroupText"),
            neuronCount: 0,

            // obj loading
            loadingProgress: 0,
            individualUnzippingProgress: [],
            individualLoadingProgress: [],

            // picking objs
            pickScene: undefined,
            // target for rendering id and depth info, when isInfo is set to true
            pickingTexture: new THREE.WebGLRenderTarget(1, 1, {
                type: THREE.FloatType}),
            // used for reading one single pixel at the position of the mouse
            pixelBuffer: new Float32Array(4),
            // used for looking up idValue's corresponding neuron OR TAG
            elementDict: {},
            rightClickMaterialDialogOpen: false,
            rightClickSelectedNeuron: undefined,

            // tags
            tagActionIndex: 0, // 1-creating, 2-deleting, 3-relocating, 4-selecting
            tagList: [],
            tagIdCount: 0,
            relocatingTag: undefined, // tag being relocated

            // used for detecting whether mouseup/touch event is from a single click or drag
            cvsMouseOrTouchPosX: 0,
            cvsMouseOrTouchPosY: 0,

            // for recording the neuronConfig data
            fileList: [],
            pointerTagShow: true,
            mouseHintShow: true
        }
    },
    computed: {
        canvasPointerIsOn() {
            return (
                this.$store.state.panelIndex===2 && this.tagActionIndex != 0
             ) || this.$store.state.panelIndex===3;
        },
        mouseHint() {
            if(this.$store.state.panelIndex===2) {
                switch(this.tagActionIndex) {
                    case 1:
                        // creating tag
                        return this.$t("m.i.mouseHintCreateTag");
                    case 2:
                        // deleting tag
                        return this.$t("m.i.mouseHintDeleteTag");
                    case 3:
                        // relocating tag
                        if(!this.relocatingTag) {
                            // tag not selected
                            return this.$t("m.i.mouseHintRelocatingTag1");
                        }
                        else {
                            return this.$t("m.i.mouseHintRelocatingTag2");
                        }
                    case 4:
                        // toggle select tag
                        return this.$t("m.i.mouseHintToggleSelectTag");
                    default:
                        return this.$t("m.i.mouseHintDblClick");
                }
            }
            else if(this.$store.state.panelIndex===3) {
                return this.$t("m.i.mouseHintNeuron");
            }
            else {
                return this.$t("m.i.mouseHintDblClick");
            }
        }
    },
    mounted() {
        this.canvas = this.$refs['main-canvas'];
        this.canvasBox = this.$refs['canvas-box'];
        this.sideBar = this.$refs['side-bar'];
        window.onresize = this.render;
        window.onbeforeunload = function (event) {
            var e = event || window.event;
            // 兼容IE8和Firefox 4之前的版本
            if (e) {
                event.returnValue = this.$t("m.a.qWindowsClose");
            }
            // Chrome, Safari, Firefox 4+, Opera 12+ , IE 9+
            return this.$t("m.a.qWindowsClose");
        }
        this.loadObj();
    },
    methods: {
        loadObj() {
            // load neuronConfig
            var path = "./model/config.json";
            axios.get('./configPath.txt').then(res => {
                path = res.data;
                this.modelPath = path.replace(/\/[^/]+$/,''); // default: '/model'
            }).finally(() => {
                axios.get(path).then(res => {
                    this.sceneIsOn = true;
                    this.loadCanvas(res.data);
                });
            })
        },
        getRomoteZipFiles(url, index, total) {
            const self = this;
            return new Promise(function(resolve, reject) {
                //  step1 请求文件
                JSZipUtils.getBinaryContent(url, {
                    callback: function(err, data) {
                        if (err) {
                            reject(err);
                        } else {
                            // loading zip file & unzipping done
                            resolve(data);
                        }
                    },
                    progress: (xhr) => {
                        self.loadingProgress -= (self.individualUnzippingProgress[index] / total);
                        self.individualUnzippingProgress[index] = xhr.loaded / xhr.total;
                        self.loadingProgress += (self.individualUnzippingProgress[index] / total);
                        self.$store.commit("loadingProgressChanged", self.loadingProgress);
                    }
                });
            })
            .then(function(files) {
                //  step2 解压
                return JSZip.loadAsync(files);
            })
            .then((files) => {
                const fileNames = Object.keys(files.files);
                return files;
            })
        },
        loadCanvas(neuronConfig) {

            this.sceneIsSet = false;

            // set canvas
            this.canvasWidth = this.canvasBox.getBoundingClientRect().width;
            this.canvasHeight = this.canvasBox.getBoundingClientRect().height;

            this.canvas.style.width =  this.canvasWidth;
            this.canvas.style.height =  this.canvasHeight;

            if(!this.sceneIsFirstSet) {
                // set 2 scenes (default + pick)
                this.scene = new THREE.Scene();
                this.pickScene = new THREE.Scene();
                this.pickScene.background = new THREE.Color(0);
            }
            this.scene.children.length = [];
            this.pickScene.children.length = [];
            // set light
            this.lights.forEach((l, i)=> {
                // load lighting settings from neuronConfig
                if(neuronConfig.scene) {
                    let neuronConfigLighting = neuronConfig.scene.lighting[i];
                    l.color = new THREE.Color(neuronConfigLighting.color);
                    l.strength = neuronConfigLighting.strength;
                }
                else {
                    l.color = new THREE.Color(1.0, 1.0, 1.0);
                }
                l.outColor = new Vector3(l.color.r, l.color.g, l.color.b);
                l.outColor.multiplyScalar(l.strength);
            })
            // set renderer
            if(!this.sceneIsFirstSet) {
                this.renderer = new THREE.WebGLRenderer({
                    canvas: this.canvas,
                    antialias: true, // 是否执行抗锯齿
                    preserveDrawingBuffer: true // snapshot
                });
            }
            this.renderer.setSize(this.canvasWidth, this.canvasHeight);
            this.renderer.renderLists.dispose();
            // cull face
            // this.renderer.setFaceCulling(THREE.CullFaceFront,THREE.FrontFaceDirectionCW);

            // set camera
            if(neuronConfig.scene && neuronConfig.scene.camera) {
                this.controlsTypeChanged(1, neuronConfig.scene.camera, true);
            }
            else {
                this.controlsTypeChanged(1, undefined, false);
            }

            // set a parent object
            if(this.loadedObj && this.loadedObj.children) {
                this.loadedObj.children.forEach(c => {
                    c = null;
                })
                this.loadedObj = null;
            }
            this.loadedObj = new THREE.Object3D();

            this.elementDict = {};
            
            // set tags (sprites)
            this.tagIdCount = 500;
            this.tagList.length = 0;
            if(neuronConfig.tags) {
                neuronConfig.tags.forEach((tagInfo) => {
                    this.addTagToScene(tagInfo);
                })
            }

            // load obj
            // clears all variables:
            this.loadingProgress = 0;   // clears progress
            this.$store.commit("loadingProgressChanged", this.loadingProgress);
            this.neuronList.length = 0; // clears neuronList
            this.fileList.length = 0; // clears fileList
            this.groupList.length = 0;  // clears groupList
            this.neuronCount = 0;
            // reset panel
            this.$store.commit('setPanelStat', {
                open: false,
                index: 0
            });
            // sets group
            var hasDefaultGroupText = false;
            if(neuronConfig.groups) {
                for(let group in neuronConfig.groups) {
                    if(group == this.defaultGroupText) {
                        hasDefaultGroupText = true;
                    }
                    this.groupList.push(group);
                }
            }
            if(!hasDefaultGroupText) {
                this.groupList.push(this.defaultGroupText);
            }

            this.$store.commit("loadingScreenFadeIn");

            // counts the total number of objs
            // ( = number of files with '.obj' suffix + sum of numbers of objs in each .zip )
            this.individualLoadingProgress = 0;

            // saves each obj str in a dictionary with key = zipName+'_'+fileName
            var objAsyncStrsFromZips = {};
            var promises = [];

            // count the number of zip files
            var zipFileCount = 0;

            neuronConfig.fileList.forEach((fileUrl, index) => {
                this.fileList.push(fileUrl);
                const suffix = CommonMethods.getFileType(fileUrl);
                if(suffix == ".zip") {
                    zipFileCount += 1;
                }
            });
            
            if(zipFileCount > 0) {
                // has files to unzip
                this.individualUnzippingProgress = new Float32Array(zipFileCount);
                this.$store.state.loadingPhase = 0;
            }
            else {
                // all files are objs
                this.$store.state.loadingPhase = 1;
            }

            var zipFileIndex = 0;

            neuronConfig.fileList.forEach((fileUrl, index) => {
                this.fileList.push(fileUrl);
                
                const suffix = CommonMethods.getFileType(fileUrl);
                if(suffix == ".zip") {
                    // get zip content
                    const zipPath = this.modelPath + `/objects/${fileUrl}`;
                    var readZipPromise = this.getRomoteZipFiles(zipPath, zipFileIndex, zipFileCount).then(files => {
                        const fileNamesInZip = Object.keys(files.files);
                        fileNamesInZip.forEach(fileName => {
                            const zippedFileSuffix = CommonMethods.getFileType(fileName);
                            if(zippedFileSuffix == ".obj") {
                                const originalName = fileUrl + '_' + fileName;
                                var objAsyncStr = files.file(fileName).async("string");
                                objAsyncStrsFromZips[originalName] = objAsyncStr;
                                this.individualLoadingProgress += 1;
                            }
                        });
                    });
                    promises.push(readZipPromise);
                    zipFileIndex += 1;
                }
                else if(suffix == ".obj") {
                    this.individualLoadingProgress += 1;
                }
            });

            Promise.all(promises).then(() => {
                // when all models have been loaded
                console.log("unzipped");
            }).catch((error) => {
                // some objects failed to load
                this.$store.commit("alert", {
                    content: this.$t("m.a.fileUnableToUnzip") + error,
                    timeout: 1000000
                });
            }).finally(() => {
                // all zip files are read
                this.$store.state.loadingPhase = 1;
                this.loadingProgress = 0;
                this.$store.commit("loadingProgressChanged", this.loadingProgress);
                this.individualLoadingProgress
                    = new Float32Array(this.individualLoadingProgress);
                this.loadModels(objAsyncStrsFromZips, neuronConfig);
            });
        },

        loadModels(objAsyncStrsFromZips, neuronConfig) {
            var promises = [];

            var self = this;
            
            // create a dictionary storing group info
            const groupData = {};
            for(let key in neuronConfig.groups) {
                neuronConfig.groups[key].forEach(val => {
                    groupData[val] = key;
                })
            }

            var objIndex = 0;

            // read and load objs in .zip files
            Object.keys(objAsyncStrsFromZips).forEach((key) => {
                const objAsyncStr = objAsyncStrsFromZips[key];
                let p = this.loadModelLocal(
                    objAsyncStr,
                    objIndex,
                    this.individualLoadingProgress.length
                ).then(result => {
                    this.addLoadedResultsToObj(result, neuronConfig, key, groupData);
                });
                promises.push(p);
                objIndex += 1;
            });

            // read and load .obj files from neuronConfig
            neuronConfig.fileList.forEach((fileUrl, index) => {
                
                var suffix = CommonMethods.getFileType(fileUrl);

                if(suffix == ".obj") {
                    // file is obj: read and load .obj file with loadModel
                    let p = this.loadModel(
                        this.modelPath + `/objects/${fileUrl}`,
                        objIndex,
                        this.individualLoadingProgress.length
                    ).then(result => {
                        this.addLoadedResultsToObj(result, neuronConfig, fileUrl, groupData);
                    })
                    promises.push(p);
                    objIndex += 1;
                }
            });

            Promise.all(promises).then(() => {
                // when all models have been loaded
                console.log("files loaded");
            }).catch((error) => {
                // some objects failed to load
                this.$store.commit("alert", {
                    content: this.$t("m.a.fileUnableToLoad") + error,
                    timeout: 1000000
                });
            }).finally(() => {
                // scene is loaded
                this.scene.add(this.loadedObj);
                
                // if camera position is unset (no config)
                if(!neuronConfig.scene || !neuronConfig.scene.camera) {

                    var boundingbox = new THREE.BoxHelper(this.loadedObj, 0xff0000);
    
                    var boundingSphere = boundingbox.geometry.boundingSphere;
                    
                    this.controlsTypeChanged(1, boundingSphere, false);
                }

                // if(neuronConfig.scene && neuronConfig.scene.camera) {
                //     // if camera's position and target is defined in config
                //     this.controls.target.fromArray(neuronConfig.scene.camera.target);
                //     this.camera.position.fromArray(neuronConfig.scene.camera.position);
                //     this.controls.position0.copy(this.camera.position);
                // }
                // else {
                //     // else: position camera to get object into view
                //     var boundingbox = new THREE.BoxHelper(this.loadedObj, 0xff0000);
    
                //     var boundingSphere = boundingbox.geometry.boundingSphere;
                //     if(boundingSphere) {
                //         this.controls.target.copy(boundingSphere.center);
                //         this.camera.position.set(boundingSphere.center.x - 2 * boundingSphere.radius,
                //                                 boundingSphere.center.y,
                //                                 boundingSphere.center.z);
                //         this.controls.position0.copy(this.camera.position);
                //     }
                // }
                // this.controlsTypeChanged(1);
                // this.controls.update();
                
                // loading ends, hide loading screen
                this.$store.commit("loadingScreenFadeOut");
                this.sceneIsSet = true;

                // sets bg color and render once; render() is within bgColorChanged
                this.bgColor = neuronConfig.scene ? neuronConfig.scene.background : "#000000";
                this.bgColorChanged(this.bgColor);
                this.sceneIsFirstSet = true;
                this.render();
            });
        },
        loadModel(url, index, total) {
            let self = this;
            return new Promise( function( resolve, reject ){
                new OBJLoader2().load(url, resolve, (xhr) => {
                        // the following assignments calculate total loading process (loadingProgress)
                        // by recording loading %s of each obj file
                        // and sum them (since files are being asynchronously loaded)
                        self.loadingProgress -= (self.individualLoadingProgress[index] / total);
                        self.individualLoadingProgress[index] = xhr.loaded / xhr.total;
                        self.loadingProgress += (self.individualLoadingProgress[index] / total);
                        self.$store.commit("loadingProgressChanged", self.loadingProgress);
                    }, (e) => {
                        reject(url);
                    }
                );
            });
        },
        loadModelLocal(objAsyncStr, index, total) {
            let self = this;
            return new Promise( function( resolve, reject ){
                objAsyncStr.then(objStr => {
                    new OBJLoader2().loadStr(objStr, resolve, (xhr) => {
                            // the following assignments calculate total loading process (loadingProgress)
                            // by recording loading %s of each obj file
                            // and sum them (since files are being asynchronously loaded)
                            self.loadingProgress -= (self.individualLoadingProgress[index] / total);
                            self.individualLoadingProgress[index] = xhr.loaded / xhr.total;
                            self.loadingProgress += (self.individualLoadingProgress[index] / total);
                            self.$store.commit("loadingProgressChanged", self.loadingProgress);
                        }, (e) => {
                            reject();
                        }
                    );
                })
            });
        },
        addLoadedResultsToObj(result, neuronConfig, key, groupData) {
            result.children.forEach((neuron, neuron_index) => {
                // 1. sets each child's material
                // 2. stores them in neuronList
                // 3. assigns each child with a unique idValue (used for picking)
                //    and stores the pair in elementDict
                // 4. sets the name of each child
                // 5. assigns each child with a group (default: "default group")
                this.neuronCount += 1;
                let idValue = this.neuronCount + 1;
                // name
                // replace the name for predifined alias, if there's any
                let originalName = CommonMethods.sansFileType(key);
                if(neuron_index > 0) {
                    // i.e. neuron file_2, neuron file_3
                    originalName += ('_' + (neuron_index+1));
                }
                let neuronName = neuronConfig.aliases ? (neuronConfig.aliases[originalName] || originalName) : originalName;
                neuron.name = neuronName;
                // neuron.geometry.computeFaceNormals();
                // get material setting from neuronConfig
                let nType = 1;
                let nprimaryColor = "#b0b0b0";
                let nSecondaryColor = "#000000";
                let nShininess = 16;
                let nVisible = true;
                let nMatSetting = neuronConfig.materials ? neuronConfig.materials[neuronName] : undefined;
                if(nMatSetting) {
                    nType = nMatSetting.type;
                    nprimaryColor = nMatSetting.primaryColor;
                    nSecondaryColor = nMatSetting.secondaryColor;
                    nShininess = nMatSetting.shininess;
                    nVisible = (nMatSetting.visible == true);
                }
                neuron.material = Shader.createMainMaterial(
                    this.camera,
                    // it **SEEMS** pixel reading could only restore value up to 6~7 decimal places
                    idValue / 1000,
                    this.lights,
                    {
                        type: nType,
                        primaryColor: nprimaryColor,
                        secondaryColor: nSecondaryColor,
                        shininess: nShininess
                    }
                );
                neuron.visible = nVisible;
                let newNeuron = {
                    index: idValue,
                    group: groupData[neuronName] || this.defaultGroupText,
                    originalName: originalName,
                    name: neuronName,
                    neuron: neuron,
                };
                this.neuronList.push(newNeuron);
                // save neuron to a dictionary with a corresponding unique idValue (for picking)
                this.elementDict[idValue] = newNeuron;
                this.loadedObj.add(neuron);
            });
        },
        addTagToScene(tagInfo) {
            // create a new sprite
            var idValue = this.tagIdCount;
            this.tagIdCount += 1;
            var generatedSpriteInfo = generateTextSprite(
                tagInfo.text,
                {
                    fontSize: tagInfo.fontSize,
                    fontColor: tagInfo.fontColor,
                    fontBold: tagInfo.fontBold,
                    fontItalic: tagInfo.fontItalic,
                    textAlign: tagInfo.textAlign,
                    borderThickness: tagInfo.borderThickness,
                    borderColor: tagInfo.hasBorder ? tagInfo.borderColor:'#00000000',
                    borderRadius: tagInfo.borderRadius,
                    backgroundColor: tagInfo.hasBackground ? tagInfo.backgroundColor:'#00000000'
                },
                idValue / 1000
            );
            var sprite = generatedSpriteInfo.sprite;
            sprite.position.set(tagInfo.position[0], tagInfo.position[1], tagInfo.position[2]);
            tagInfo.sprite = sprite;
            tagInfo.ruler = generatedSpriteInfo.ruler;
            tagInfo.canvas = generatedSpriteInfo.canvas;
            tagInfo.idValue = idValue;
            // save tag to a dictionary with a corresponding unique idValue (for picking)
            this.elementDict[idValue] = tagInfo;
            this.tagList.push(tagInfo);
            this.loadedObj.add(sprite);
        },
        /**
         * @param presets either config.scene.camera or boundingSphere or undefiend
         */
        controlsTypeChanged(controlsTypeValue, presets, isCameraPreset) {
            if(isCameraPreset && presets && presets.type !== undefined) {
                // overrides default controlsTypeValue if it's configured in presets
                controlsTypeValue = presets.type;
            }
            // generate camera
            if(!this.camera) {
                this.camera = new THREE.PerspectiveCamera(
                    this.fov,
                    this.canvasWidth/this.canvasHeight,
                    this.near,
                    this.far
                );
            }

            // position
            var tempCameraPosition = new Vector3();
            if(isCameraPreset) {
                // has config
                tempCameraPosition.fromArray(presets.position);
            }
            else if(presets) {
                // has boundingSphere
                tempCameraPosition.set(
                    presets.center.x - 2 * presets.radius,
                    presets.center.y,
                    presets.center.z);
            }
            else {
                // change camera type, or no preset is present
                tempCameraPosition.copy(this.camera.position);
            }

            // target
            var tempControlsTarget = new Vector3();
            if(isCameraPreset) {
                // has config
                tempControlsTarget.fromArray(presets.target);
            }
            else if(presets) {
                // has boundingSphere
                tempControlsTarget.set(
                    presets.center.x,
                    presets.center.y,
                    presets.center.z);
            }
            else if(this.controls) {
                // change camera type, or no preset is present
                tempControlsTarget.copy(this.controls.target);
            }

            if(this.controls) {
                this.controls.dispose();
            }
            
            // up
            var tempCameraUp = new Vector3();
            if(isCameraPreset) {
                // has config
                tempCameraUp.fromArray(presets.up);
            }
            else {
                // change camera type, or no preset is present
                tempCameraUp.copy(this.camera.up);
            }

            this.camera.position.copy(tempCameraPosition);
            this.camera.up.copy(tempCameraUp);
            this.camera.updateProjectionMatrix();

            if(controlsTypeValue == 0) {
                this.camera.up.set(0, 1, 0);
                this.controls = new OrbitControls(this.camera, this.canvas);
            }
            else {
                this.controls = new TrackballControls(this.camera, this.canvas);
                this.controls.staticMoving = true;
                // set panSpeed
                var panSpeed = 0.5;
                if(isCameraPreset && presets.panSpeed) {
                    panSpeed = presets.panSpeed;
                }
                this.controls.panSpeed = panSpeed;
            }
            
            this.controls.target.copy(tempControlsTarget);
            this.controls.removeEventListener('change', this.render);
            this.controls.addEventListener('change', this.render);
            this.controls.update();

            this.$store.commit("setControls", this.controls);
        },
        renderNeedsResize() {
            this.canvasWidth = this.canvasBox.getBoundingClientRect().width;
            this.canvasHeight = this.canvasBox.getBoundingClientRect().height;
            const needResize = this.canvas.width !== this.canvasWidth || this.canvas.height !== this.canvasHeight;
            if (needResize) {
                this.sideBar.resized();
                return [this.canvasWidth, this.canvasHeight];
            }
        },
        render() {
            let newSize = this.renderNeedsResize();
            if(this.sceneIsSet) {
                if (newSize !== undefined) {
                    this.renderer.setSize(newSize[0], newSize[1], false);
                    this.canvas.style.width = newSize[0] + 'px';
                    this.canvas.style.height = newSize[1] + 'px';
                    this.camera.aspect = this.canvas.clientWidth / this.canvas.clientHeight;
                    this.camera.updateProjectionMatrix();
                    // this.controls.handleResize();
                }
                this.renderer.setRenderTarget(null);
                this.renderer.render(this.scene, this.camera);
            }
        },
        // scene
        bgColorChanged(color) {
            this.bgColor = color;
            this.renderer.setClearColor(this.bgColor, 1.0);
            this.render();
        },
        onTransitionEnd( event ) {
            // event.target.remove();
            event.target.classList.remove( 'fade-in' );
        },
        /**
         * used for picking element with mouse
         */
        getCanvasRelativePosition(event) {
            const rect = this.canvas.getBoundingClientRect();
            return {
                x: (event.clientX - rect.left) * this.canvas.width  / rect.width,
                y: (event.clientY - rect.top ) * this.canvas.height / rect.height,
            };
        },
        /**
         * search for any valid neuron info within a certain offset from original coords
         */
        getClickInfo(e) {
            // original position
            var originalInfo = this.getClickInfoOnce(e);
            if(originalInfo[0]) {
                return originalInfo;
            }
            var maxOffset = 5;
            var tempE = {
                clientX: e.clientX,
                clientY: e.clientY
            };
            var tempInfo = undefined;
            for(let tempOffset = 1; tempOffset <= maxOffset; tempOffset += 1) {
                tempE.clientX -= tempOffset;
                tempInfo = this.getClickInfoOnce(tempE);
                if(tempInfo[0]) {
                    return tempInfo;
                }
                tempE.clientX += 2 * tempOffset;
                tempInfo = this.getClickInfoOnce(tempE);
                if(tempInfo[0]) {
                    return tempInfo;
                }
                tempE.clientX -= tempOffset;
                tempE.clientY -= tempOffset;
                tempInfo = this.getClickInfoOnce(tempE);
                if(tempInfo[0]) {
                    return tempInfo;
                }
                tempE.clientY += 2 * tempOffset;
                tempInfo = this.getClickInfoOnce(tempE);
                if(tempInfo[0]) {
                    return tempInfo;
                }
                tempE.clientY -= tempOffset;
            }
            // if no valid object is within the offset
            return originalInfo;
        },
        getClickInfoOnce(e) {
            const clickPos = this.getCanvasRelativePosition(e);
            const pixelRatio = this.renderer.getPixelRatio();
            // pick
            this.camera.setViewOffset(
                this.renderer.getContext().drawingBufferWidth,   // full width
                this.renderer.getContext().drawingBufferHeight,  // full top
                clickPos.x * pixelRatio | 0,             // rect x
                clickPos.y * pixelRatio | 0,             // rect y
                1,                                          // rect width
                1,                                          // rect height
            );
            // renders info to pickingTexture
            this.renderer.setClearColor(this.blackColor, 1.0);
            this.renderer.setRenderTarget(this.pickingTexture);
            this.renderer.antialias = false;
            this.pickScene.add(this.loadedObj);
            this.loadedObj.children.forEach(n => {
                n.material.uniforms.isInfo.value = true;
            })
            this.renderer.render(this.pickScene, this.camera);
            this.renderer.readRenderTargetPixels(
                this.pickingTexture,
                0,   // x
                0,   // y
                1,   // width
                1,   // height
                this.pixelBuffer);
            // gets the picked neuron
            var restoredIdValue = Math.round(this.pixelBuffer[0] * 1000);
            // renders scene back to original canvas
            this.renderer.setRenderTarget(null);
            this.renderer.setClearColor(this.bgColor, 1.0);
            this.camera.clearViewOffset();
            this.scene.add(this.loadedObj);
            this.loadedObj.children.forEach(n => {
                n.material.uniforms.isInfo.value = false;
            })
            this.renderer.antialias = true;

            return [
                this.elementDict[restoredIdValue],
                this.pixelBuffer[1],
                this.pixelBuffer[2],
                this.pixelBuffer[3]
            ];
        },
        /**
         * records touch position when mouse is down
         */
        onCanvasMouseDown(e) {
            // preventDefault: an operation I've removed in node_module's OrbitControls (line 827)
            // (see patch file)
            e.preventDefault();
            this.cvsMouseOrTouchPosX = e.clientX;
            this.cvsMouseOrTouchPosY = e.clientY;
        },
        /**
         * used when app is opened in mobile devices; records touch position
         */
        onCanvasTouchStart(e) {
            this.cvsMouseOrTouchPosX = e.touches[0].clientX;
            this.cvsMouseOrTouchPosY = e.touches[0].clientY;
        },
        /**
         * pass changedTouches position to onCanvasClickedOrTouched
         */
        onCanvasTouchEnd(e) {
            this.onCanvasClickedOrTouched(e.changedTouches[0]);
        },
        /**
         * when dblclick: reposition camera target?
         */
        onCanvasDblClick(e) {
            if(this.$store.state.panelIndex !== 3) {
                var info = this.getClickInfo(e);
                if(info[0]) {
                    if(info[0].neuron !== undefined) {
                        // clicked on neuron
                        this.controls.target.set(info[1],info[2],info[3]);
                        this.controls.update();
                    }
                    else {
                        // clicked on tag
                        this.controls.target.fromArray(info[0].position);
                        this.controls.update();
                    }
                }
            }
        },
        onCanvasClickedOrTouched(e) {
            if(e.preventDefault) {
                e.preventDefault();
            }
            if(e.clientX !== this.cvsMouseOrTouchPosX || e.clientY !== this.cvsMouseOrTouchPosY) {
                // has been dragged; not a single click
                return;
            }
            var info = undefined;
            if(this.$store.state.panelIndex == 2) {
                // draw panel open
                info = this.getClickInfo(e);
                if(this.tagActionIndex == 1) {
                    // get info at picked position; create tag
                    // tells DrawPanel to generate a tag at pointed position
                    this.$refs["drawPanel"].tagPositionSet(info, this.tagIdCount);
                    this.tagIdCount += 1;
                }
                else if(this.tagActionIndex == 2) {
                    // get info at picked position; delete tag
                    if(!info[0] || !info[0].sprite) {
                        // didn't pick anything, or picked neuron instead of tag
                        this.$store.commit("alert", {
                            content: this.$t("m.a.tagNotSelected"),
                        });
                    }
                    else {
                        // having picked the tag about to be removed
                        var flag = confirm(this.$t("m.a.qDoRemoveTag"));
                        if(flag) {
                            this.deleteTag(info[0]);
                            this.render();
                        }
                    }
                }
                else if(this.tagActionIndex == 3) {
                    if(!this.relocatingTag) {
                        // select a tag to relocate
                        if(!info[0] || !info[0].sprite) {
                            // didn't pick anything, or picked neuron instead of tag
                            this.$store.commit("alert", {
                                content: this.$t("m.a.tagNotSelected"),
                            });
                        }
                        else {
                            this.relocatingTag = info[0];
                        }
                    }
                    else {
                        // select a new location to relocate relocatingTag to
                        if(!info[0] || !info[0].neuron) {
                            // didn't pick anything, or picked tag instead of neuron
                            this.$store.commit("alert", {
                                content: this.$t("m.a.newLocationNotSelected"),
                            });
                        }
                        else {
                            this.relocatingTag.position[0] = info[1];
                            this.relocatingTag.position[1] = info[2];
                            this.relocatingTag.position[2] = info[3];
                            this.relocatingTag.sprite.position.fromArray(this.relocatingTag.position);
                            this.render();
                            this.cancelRelocatingTag();
                        }
                    }
                }
                else if(this.tagActionIndex == 4) {
                    // toggle select tag
                    if(info[0] && info[0].sprite) {
                        this.$refs["drawPanel"].toggleSelectTagChange(info[0]);
                    }
                }
            }
            else if(this.$store.state.panelIndex == 3) {
                // neuron panel open; initiate neuron selection
                // get info at picked position
                info = this.getClickInfo(e);
                if(e.button == 2) {
                    // on right click
                    // sets neuron material
                    if(info[0] && info[0].neuron) {
                        this.rightClickMaterialDialogOpen = true;
                        this.rightClickSelectedNeuron = info[0].neuron;
                    }
                }
                else if(info[0] && info[0].neuron) {
                    // on left click or mouse click
                    // let neuron panel have the picked neuron and do its operation
                    this.$refs["neuronPanel"].neuronPicked(info[0]);
                }
            }
        },
        /**
         *  used for draw panel
         */
        startCreatingTag() {
            this.tagActionIndex = 1;
            this.mouseHintShow = true;
            this.$store.commit('setPanelStat', {
                open: false,
            })
        },
        /**
         *  used for draw panel
         */
        endCreatingTag(tagInfo) {
            this.resetTagActionIndex();
            if(tagInfo && tagInfo.sprite) {
                this.elementDict[tagInfo.idValue] = tagInfo;
                this.tagList.push(tagInfo);
                this.loadedObj.add(tagInfo.sprite);
                this.render();
            }
            this.$store.commit('setPanelStat', {
                open: true,
            })
        },
        /**
         *  used for draw panel
         */
        startDeletingTag() {
            this.tagActionIndex = 2;
            this.mouseHintShow = true;
            this.$store.commit('setPanelStat', {
                open: false,
            })
        },
        cancelDeletingTag() {
            this.resetTagActionIndex();
            this.$store.commit('setPanelStat', {
                open: true,
            })
        },
        deleteTag(tagInfo) {
            var tagIndex = this.tagList.indexOf(tagInfo);
            if(tagIndex >= 0) {
                this.tagList.splice(tagIndex, 1);
            }
            this.loadedObj.remove(tagInfo.sprite);
        },
        /**
         *  used for draw panel
         */
        startRelocatingTag() {
            this.tagActionIndex = 3;
            this.mouseHintShow = true;
            this.$store.commit('setPanelStat', {
                open: false,
            })
        },
        cancelRelocatingTag() {
            this.resetTagActionIndex();
            this.relocatingTag = undefined;
            this.$store.commit('setPanelStat', {
                open: true,
            })
        },
        /**
         *  used for draw panel
         */
        startToggleSelectTag() {
            this.tagActionIndex = 4;
            this.mouseHintShow = true;
        },
        cancelToggleSelectTag() {
            this.resetTagActionIndex();
        },
        resetTagActionIndex() {
            this.tagActionIndex = 0;
        },
        drawPanelEnded() {
            this.resetTagActionIndex();
            this.tagList.forEach(tagInfo => {
                tagInfo.sprite.material.uniforms.isSelected.value = false;
            });
            this.render();
        },
        /**
         * save all neuronConfigs as a json file
         */
        saveConfigJson() {
            try {
                var isFileSaverSupported = !!new Blob;
            } catch (e) {
                this.$store.commit("alert", {
                    content: this.$t("m.a.browserVersionTooOld")
                });
            }
            var outputFile = {};
            // fileList
            outputFile["fileList"] = this.fileList;
            // aliases & groups
            var outAliasDict = {};
            var outGroupDict = {};
            var outMaterialDict = {};
            this.groupList.forEach(groupName => {
                    outGroupDict[groupName] = [];
            })
            this.neuronList.forEach(item => {
                var gr = outGroupDict[item.group];
                if(gr!= undefined) {
                    gr.push(item.neuron.name);
                }
                else {
                    outGroupDict[item.group] = [item.neuron.name];
                }
                outAliasDict[item.originalName] = item.neuron.name;
                var mat = item.neuron.material;
                outMaterialDict[item.neuron.name] = {
                    type: mat.uniforms.type.value,
                    primaryColor: "#" + mat.uniforms.primaryColor.value.getHexString(),
                    secondaryColor: "#" + mat.uniforms.secondaryColor.value.getHexString(),
                    shininess: mat.uniforms.shininess.value,
                    visible: item.neuron.visible
                }
            })
            outputFile["aliases"] = outAliasDict;
            outputFile["groups"] = outGroupDict;
            outputFile["materials"] = outMaterialDict;
            var outLightingList = [];
            this.lights.forEach(light => {
                outLightingList.push({
                    color: "#" + light.color.getHexString(),
                    strength: light.strength
                })
            });
            var controlsType = this.$store.getters.controlsType;
            var panSpeed = 0.5;
            if(controlsType == 1 && this.controls.panSpeed) {
                panSpeed = this.controls.panSpeed;
            }
            var outCamera = {
                type: controlsType,
                panSpeed: panSpeed,
                position: this.camera.position.toArray(),
                target: this.controls.target.toArray(),
                up: this.camera.up.toArray(),
            };
            outputFile["scene"] = {
                background: this.bgColor,
                lighting: outLightingList,
                camera: outCamera
            };
            var outTagList = [];
            this.tagList.forEach(tag => {
                outTagList.push({
                    position: tag.position,
                    text: tag.text,
                    fontItalic: tag.fontItalic,
                    fontBold: tag.fontBold,
                    fontSize: tag.fontSize,
                    textAlign: tag.textAlign,
                    fontColor: tag.fontColor,
                    hasBorder: tag.hasBorder,
                    borderColor: tag.borderColor,
                    borderRadius: tag.borderRadius,
                    borderThickness: tag.borderThickness,
                    hasBackground: tag.hasBackground,
                    backgroundColor: tag.backgroundColor
                })
            })
            outputFile["tags"] = outTagList;
            var outputJson = JSON.stringify(outputFile, null, 4);
            download(outputJson, "config.json", "text/plain");
        },
        saveScreenshot() {
            var strMime = "image/jpeg";
            var imgData = this.renderer.domElement.toDataURL(strMime);
            var link = document.createElement("a");
            link.download = "neuron.png";
            link.href = imgData;
            link.click();
        },
    },
    beforeDestroy() {
        window.removeEventListener("resize", this.render);
    }
}
</script>

<style scoped>
.canvas-container {
    position: relative;
    height: 100%;
    width: 100%;
    /* border: 1px solid black; */
    display: flex;
    flex-direction: column;
    align-items: stretch;
    overflow: hidden;
}
.canvas-box {
    height: 100%;
    width: 100%;
    position: relative;
    display: flex;
}

.canvas--pointer-on {
    cursor: pointer;
}
.pointer-tag {
    display: flex;
    position: absolute;
    top: 30px;
    left: 30px;
    z-index: 1;
    color: #ffffff;
    user-select: none;
    -moz-user-select: -moz-none;
    -webkit-user-select: none;
    -ms-user-select: none;
}
.editor-right-bar >>> .right-panel {
    overflow-y: scroll;
}
</style>