<template>
    <v-dialog
        v-model="lightsDialogOpen"
        overlay-opacity="0"
        width="300"
    >
        <template>
            <v-row class="v-expansion-panel-header" no-gutters>{{$t("m.i.lightings")}}</v-row>
            <v-flex tile flat class="pa-4 lights-dialog-card">
                <!-- lights -->
                <div class="mx-4" v-for="(v, i) in lights" :key="i" >
                    <v-row class="mt-2" no-gutters>
                        <v-col
                        class="font-weight-bold d-flex align-center"
                        cols="4">{{$t("m.i.lightings")}}{{i+1}}</v-col>
                        <v-col cols="8">
                            <nf-color-three
                            :name="$t('m.i.color')"
                            v-model='lights[i].color'
                            @input="lightValChanged(i)">
                            </nf-color-three>
                            <nf-toggle
                            :name="$t('m.i.strength')"
                            v-model="lights[i].strength"
                            @input="lightValChanged(i)"
                            :min="0.0"
                            :max="2.0"
                            :step="0.05"></nf-toggle>
                        </v-col>
                    </v-row>
                    <v-divider class="mt-2" v-if="i < lights.length - 1"></v-divider>
                </div>
            </v-flex>
            <v-row class="v-expansion-panel-header" no-gutters>{{$t("m.i.camera")}}</v-row>
            <v-flex class="pa-4 px-8 lights-dialog-card">
                <v-select
                    :items="cameraTypes"
                    v-model="selectedCameraType"
                    :label="$t('m.i.cameraType')"
                    @change="controlsTypeChanged"
                ></v-select>
                <nf-toggle
                :name="$t('m.i.cameraPanSpeed')"
                :disabled="selectedCameraType==0"
                v-if="lightsDialogOpen"
                v-model="$store.state.controls.panSpeed"
                :min="0.1"
                :max="2.0"
                :step="0.05"></nf-toggle>
            </v-flex>
        </template>
    </v-dialog>
</template>

<script>

export default {
    props: {
        lights: {
            type: Array,
            default: () => []
        },
    },
    data() {
        return {
            lightsDialogOpen: false,
            cameraTypes: [
                {
                    value: 0,
                    text: this.$t('m.i.cameraType1')
                },
                {
                    value: 1,
                    text: this.$t('m.i.cameraType2')
                }
            ],
            selectedCameraType: 0,
        }
    },
    methods: {
        open() {
            this.lightsDialogOpen = true;
            if(this.$store.getters.controlsType == 1) {
                this.selectedCameraType = 1;
            }
            else {
                this.selectedCameraType = 0;
            }
        },
        lightValChanged(i) {
            this.$emit("lightValChanged", i);
        },
        controlsTypeChanged(v) {
            this.$emit("controlsTypeChanged", v);
        }
    },
}
</script>

<style scoped>
.v-expansion-panel-header {
    background: #c0c0c0;
}
.lights-dialog-card {
    background: #ffffff;
}
</style>