<template>
    <v-row class="ma-0 pa-0">
        <input class="small-input" type="number" v-model="value.x" @input="valueChanged" />
        <input class="small-input" type="number" v-model="value.y" @input="valueChanged" />
        <input class="small-input" type="number" v-model="value.z" @input="valueChanged" />
    </v-row>
</template>

<script>
export default {
    name: 'NfVectorInput',
    props: {
        value: Object
    },
    methods: {
        valueChanged() {
            this.$emit('change', this.value);
        }
    }
}
</script>

<style scoped>
.small-input {
    width: 50px;
}
</style>