<template>
    <v-radio-group :value="value" @change="change">
        <v-radio
        v-for="(k, i) in radios"
        :key="i"
        :label="k.label"
        :disabled="k.disabled"
        :value="k.value != undefined ? k.value : i"></v-radio>
    </v-radio-group>
</template>

<script>
export default {
    props: {
        value: {
            type: Number,
            default: 0
        },
        radios: {
            type: Array,
            default: () => {
                return [
                    {
                        label: "radio 1",
                        disabled: false
                    },
                    {
                        label: "radio 2",
                        disabled: false
                    },
                    {
                        label: "radio 3",
                        disabled: true
                    },
                ]
            }
        }
    },
    methods: {
        change(val) {
            this.$emit('input', val);
        }
    }
}
</script>