<template>
    <v-row no-gutters class="justify-space-between nf-buttons" :class="{'vertical-btns': btns.length >= 3 || vertical}">
        <v-btn class="nf-btn"
        tile
        v-for="(k, i) in btns" :key="i" @click="btnClicked(i, k.method)" :color="k.col">
            {{k.label}}
        </v-btn>
    </v-row>
</template>

<script>
export default {
    props: {
        btns: {
            type: Array,
            default: () => [
                {label: 'btn1', col: 'red', method: 'btn1Clicked'},
                {label: 'btn2', col: 'grey', method: 'btn2Clicked'},
            ]
        },
        vertical: {
            type: Boolean,
            default: false,
        }
    },
    methods: {
        btnClicked(i, method) {
            if (method != undefined) {
                this.$emit(method);
            }
            else {
                this.$emit("btnClicked", i);
            }
        }
    }
}
</script>

<style scoped>
.nf-btn {
    margin: 6px;
    text-transform: inherit;
}
.vertical-btns {
    flex-direction: column;
    margin-top: 0px;
    margin-bottom: 4px;
}
.justify-space-between:not(.vertical-btns) >>> .nf-btn {
    flex: 1;
}
.nf-buttons {
    margin-top: 4px;
}
</style>