<template>
    <v-data-table
        class="neuron-table"
        v-model="selectedNeurons"
        @item-selected="itemSelected"
        @toggle-select-all="toggleSelectedAll"
        :headers="headers"
        :items="neuronList"
        item-key="index"
        show-select
        dense
        group-by="group"
        show-group-by
        mobile-breakpoint="0"
        disable-pagination
        hide-default-footer
    >
        <template v-slot:item.name="{ item }">
            <v-edit-dialog  v-if="isEditor"
            :return-value.sync="item.neuron.name"
            >
            {{ item.neuron.name }}
                <template v-slot:input>
                    <v-text-field
                    v-model="item.neuron.name"
                    label="Edit"
                    single-line
                    counter
                    ></v-text-field>
                </template>
            </v-edit-dialog>
            <div v-else class="name-display" :title="item.neuron.name">
                <input
                class="name-input"
                :value="item.neuron.name"/>
            </div>
        </template>
        <template v-slot:item.settings="{ item }">
            <v-row no-gutters class="settings-btns-row justify-center">
                <!-- <nf-color-three
                    :small="true"
                    v-model="item.neuron.material.uniforms.primaryColor.value"
                    @input="valueChanged"
                ></nf-color-three> -->
                <v-icon
                :title="$t('m.i.material')"
                @click="$emit('openMaterialDialog', item.neuron)
                ">
                    mdi-alpha-m-box
                </v-icon>
                <!-- <v-icon @click="wireframeChanged(item.neuron)">
                    {{wireframeIcons[item.neuron.material.wireframe+0]}}
                </v-icon> -->
                <v-icon
                :title="$t('m.i.visibility')"
                @click="visibilityChanged(item.neuron)">
                    {{visibilityIcons[item.neuron.visible+0]}}
                </v-icon>
                <v-icon
                :title="$t('m.i.center')"
                @click="focusClicked(item)">mdi-camera</v-icon>
            </v-row>
        </template>
        <!-- <template v-slot:item.color="{ item }">
            <nf-color-three
                :small="true"
                v-model="item.neuron.material.uniforms.primaryColor.value"
                @input="valueChanged"
            ></nf-color-three>
        </template> -->
        <!-- <template v-slot:item.material="{ item }">
            <nf-button @click="$emit('openMaterialDialog', item.neuron)" name="设置"></nf-button>
        </template>
        <template v-slot:item.wireframe="{ item }">
            <v-simple-checkbox :ripple="false"
                v-model="item.neuron.material.wireframe"
                @input="valueChanged"
            ></v-simple-checkbox>
        </template>
        <template v-slot:item.visible="{ item }">
            <v-icon @click="visibilityChanged(item.neuron)">
                {{visibilityIcons[item.neuron.visible+0]}}
            </v-icon>
        </template>
        <template v-slot:item.focus="{ item }">
            <v-icon @click="focusClicked(item)">mdi-camera</v-icon>
        </template> -->
        <template v-slot:group.header="{group, items}">
            <td :colspan="headers.length+1" class="group-row text-start">
                <v-row no-gutters>
                    <v-icon class="group-icon" @click="checkGroup($event, group)">
                        {{groupCheckedIcons[isChecked(items)]}}
                    </v-icon>
                    <v-edit-dialog v-if="isEditor">
                        <span class="ms-2"><i>{{group}}</i></span>
                        <template v-slot:input>
                            <v-text-field
                            :value="group"
                            @input="groupNameChanged($event, group)"
                            label="Edit"
                            single-line
                            counter
                            ></v-text-field>
                        </template>
                    </v-edit-dialog>
                    <span v-else class="ms-2"><i>{{group}}</i></span>
                </v-row>
                
            </td>
        </template>
    </v-data-table>
</template>


<script>
export default {
    props: {
        router: {
            type: String,
            default: "viewer"
        },
        neuronList: {
            default: () => [],
            type: Array
        },
        value: {
            default: () => [],
            type: Array
        },
    },
    data() {
        return {
            isEditor: false,
            openedPanels: [0, 1],
            selectedNeurons: [],
            headers: [
                {
                    text: this.$t("m.i.name"),
                    value: 'name',
                    align: 'center',
                    width: 100,
                    groupable: false,
                    sortable: true,
                },
                {
                    text: this.$t("m.i.settings"),
                    value: 'settings',
                    align: 'center',
                    groupable: false,
                    sortable: false,
                },
                // {
                //     text: 'material',
                //     value: 'material',
                //     align: 'center',
                //     width: 100,
                //     groupable: false,
                //     sortable: false,
                // },
                // {
                //     text: 'wireframe',
                //     value: 'wireframe',
                //     align: 'center',
                //     width: 100,
                //     groupable: false,
                //     sortable: false,
                // },
                // {
                //     text: 'visibility',
                //     value: 'visible',
                //     align: 'center',
                //     width: 100,
                //     groupable: false,
                //     sortable: false,
                // },
                // {
                //     text: 'focus',
                //     value: 'focus',
                //     align: 'center',
                //     width: 70,
                //     groupable: false,
                //     sortable: false,
                // },
            ],
            wireframeIcons: [
                'mdi-rhombus-split',
                'mdi-rhombus-split-outline'
            ],
            groupCheckedIcons: [
                'mdi-checkbox-marked',
                'mdi-checkbox-blank-outline',
                'mdi-minus-box'
            ],
            visibilityIcons: [
                'mdi-eye-off',
                'mdi-eye'
            ]
        }
    },
    created() {
        this.forceRefreshSelectedNeurons();
        if(this.router == "viewer") {
            this.isEditor = false;
        }
        else {
            this.isEditor = true;
        }
    },
    methods: {
        /**
         * used in NeuronPanel for force refreshing selectedNeurons
         */
        forceRefreshSelectedNeurons() {
            this.selectedNeurons = this.value;
        },
        itemSelected(e) {
            if(e) {
                this.setSelectMaterial(e.item, e.value);
            }
            this.$nextTick(()=> {
                this.$emit("input", this.selectedNeurons);
            })
        },
        /**
         * in the original method vuetify offers,
         * toggle-select-all only selects/deselects all items in the current page
         */
        toggleSelectedAll(val) {
            if(val.value) {
                this.selectedNeurons = this.neuronList;
                this.neuronList.forEach(item => {
                    this.setSelectMaterial(item, true);
                })
            }
            else{
                this.selectedNeurons = [];
                this.neuronList.forEach(item => {
                    this.setSelectMaterial(item, false);
                })
            }
            this.itemSelected();
        },
        valueChanged() {
            this.$emit("renderOnce");
        },
        checkGroup(event, group) {
            // TODO: dangerous, classList might be in another order
            if(!event.target.classList.contains(this.groupCheckedIcons[0])) {
                // some or none checked; check all
                this.neuronList.forEach(item => {
                    if(item.group == group && this.selectedNeurons.indexOf(item) < 0) {
                        // item belongs to the group and has not been selected
                        this.selectedNeurons.push(item);
                        this.setSelectMaterial(item, true);
                    }
                })
            }
            else {
                // all checked; uncheck all
                this.neuronList.forEach(item => {
                    let indexOfNeuron = this.selectedNeurons.indexOf(item);
                    if(item.group == group && indexOfNeuron >= 0) {
                        // item belongs to the group and has not been selected
                        this.selectedNeurons.splice(indexOfNeuron, 1);
                        this.setSelectMaterial(item, false);
                    }
                })
            }
            this.itemSelected();
        },
        /**
         * verifies if all items in the group are checked
         */
        isChecked(groupItems) {
            var hasChecked = false;
            var hasNotChecked = false;
            for(let i = 0; i < groupItems.length; i++) {
                if(this.selectedNeurons.indexOf(groupItems[i]) < 0) {
                    hasNotChecked = true;
                }
                else {
                    hasChecked = true;
                }
            }
            if (hasChecked && hasNotChecked) {
                return 2;
            }
            else if (hasChecked && !hasNotChecked) {
                return 0;
            }
            else if (!hasChecked && hasNotChecked) {
                return 1;
            }
            return 1;
        },
        /**
         * changes wireframe
         */
        // wireframeChanged(neuron) {
        //     neuron.material.wireframe = !neuron.material.wireframe;
        //     this.valueChanged();
        // },
        /**
         * changes visibility
         */
        visibilityChanged(neuron) {
            neuron.visible = !neuron.visible;
            this.valueChanged();
        },
        /**
         * moves camera to focus on this item(mesh)
         */
        focusClicked(item) {
            this.$emit('focusOnItem', item);
        },
        /**
         * set material's isSelected value when item is selected/deselected
         */
        setSelectMaterial(item, val) {
            item.neuron.material.uniforms.isSelected.value = val;
            this.valueChanged();
        },
        /**
         * group name changed; change all group names of neurons and change groupList
         */
        groupNameChanged(newVal, oldVal) {
            this.neuronList.forEach(item => {
                if(item.group === oldVal) {
                    item.group = newVal;
                }
            })
            // change group name in groupList
            this.$emit("groupNameChanged", [newVal, oldVal]);
        }
    }
}
</script>

<style scoped>
.neuron-table {
    max-height: 480px;
    width: 750px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.neuron-table >>> th, .neuron-table >>> td {
    padding: 0 2px !important;
}
.neuron-table >>> .v-data-table__wrapper {
    flex: 1;
    overflow-y: scroll;
    overflow-x: auto;
    padding: 5px 0;
}
.neuron-table >>> .v-small-dialog__activator {
    width: 140px;
}
.neuron-table >>> .name-display {
    max-width: 140px;
    display: block;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    text-align: left !important;
}
.list-content >>> .v-expansion-panel-content__wrap {
    padding: 16px 0;
}
.group-icon {
    cursor: pointer;
}
.group-row {
    flex-wrap: nowrap;
    width: 100%;
    background-color: #e0e0e0;
}
.settings-btns-row {
    flex-wrap: nowrap;
}
.name-input {
    width: 100%;
}
</style>