<template>
    <v-row no-gutters>
        <v-col class="nf-toggle--name" :class="{'text--disabled': disabled}">{{name}}</v-col>
        <v-col class="d-flex align-center nf-toggle--toggle">
            <v-slider class="my-slider" :value='value' @change='change' :min='min' :max='max' :step='step'
            :thumb-label="true"
            :disabled="disabled"
            hide-details
            persistent-hint></v-slider>
        </v-col>
    </v-row>
</template>

<script>
export default {
    props: {
        name: String,
        value: {
            type: Number,
            default: 50
        },
        disabled: {
            type: Boolean,
            default: false,
        },
        step: {
            tyep: Number,
            default: 1.0
        },
        min: {
            tyep: Number,
            default: 1.0
        },
        max: {
            tyep: Number,
            default: 100.0
        },
    },
    methods: {
        change(val) {
            this.$emit("input", val);
        }
    }
}
</script>

<style scoped>
.my-slider >>> .v-slider {
    margin-left: 0;
    margin-right: 0;
    min-height: 0;
}
.nf-toggle--name {
    white-space: nowrap;
    width: fit-content;
    margin-right: 8px;
}
.nf-toggle--toggle {
    flex: 1;
}
</style>