<template>
    <v-container class="right-panel-container">
        <nl-title title="文件"></nl-title>
        <v-expansion-panels class="right-panel" tile accordion multiple v-model="openedPanels">
            <v-expansion-panel>
                <v-expansion-panel-header>{{$t('m.i.load')}}</v-expansion-panel-header>
                <v-expansion-panel-content>
                    <nf-buttons
                    vertical
                    :btns="loadObjBtn"
                    @loadObj="loadObj"
                    @loadObjLocal="loadObjLocal"></nf-buttons>
                    <input
                    ref="localFileInput"
                    type="file"
                    v-show="false"
                    @change="localFileLoaded"
                    />
                </v-expansion-panel-content>
            </v-expansion-panel>
            <v-expansion-panel>
                <v-expansion-panel-header>{{$t('m.i.save')}}</v-expansion-panel-header>
                <v-expansion-panel-content>
                    <nf-buttons
                    vertical
                    :btns="saveObjBtn"
                    @saveConfigJson="saveConfigJson"></nf-buttons>
                    <v-row no-gutters class="pa-1">
<p><b>config的用法：</b></p><p>Config文件由<code>configPath.txt</code>指定，该文件包含config文件与之的相对路径和名称，如
<v-card light width="100%" class="ma-1 pa-1">
<pre>./model/config.json</pre>
</v-card>。</p>
<p>初始时的config文件应设置如下
<v-card light width="100%" class="ma-1 pa-1">
<pre>
{
    "fileList": [
        "file_1.obj",
        "file_2.zip"
    ]
}</pre>
</v-card>
其中<code>file_1</code>、<code>file_2</code>等均为位于与config同级的<code>/objects</code>文件夹下需要包含在工程中的文件；文件后缀支持.zip和.obj两种，其中.zip文件为压缩后的若干.obj文件。</p>
<p>进行各种修改后，点击“保存config”，将原config替换为下载得到的文件，并将其路径和名称与<code>configPath.txt</code>相匹配。</p>
                    </v-row>
                </v-expansion-panel-content>
            </v-expansion-panel>
            
        </v-expansion-panels>
    </v-container>
</template>

<script>
export default {
    data() {
        return {
            openedPanels: [0, 1],
            loadObjBtn: [
                {
                    label: this.$t("m.i.reloadNeuron"),
                    method: "loadObj"
                },
            ],
            saveObjBtn: [
                {
                    label: this.$t("m.i.saveConfig"),
                    method: "saveConfigJson"
                }
            ]
        }
    },
    methods: {
        loadObj() {
            var flag = confirm(this.$t("m.a.qDoReloadNeuron"));
			if(flag) {
                this.$emit("loadObj");
            }
        },
        loadObjLocal() {
            this.$refs["localFileInput"].click();
        },
        localFileLoaded(e) {
            this.$emit("loadObjLocal");

        },
        saveConfigJson() {
            this.$emit("saveConfigJson");
        }
    }

}
</script>

<style scoped>

</style>