<template>
    <v-dialog
        v-model="groupDialogOpen"
        overlay-opacity="0"
        width="min-content"
        height="400px"
        persistent
    >
        <template>
            <v-card class="pa-4 group-dialog-card">
                <v-row no-gutters>
                    <v-data-table
                    class="group-table"
                    v-model="selectedNeurons"
                    @item-selected="itemSelected"
                    @toggle-select-all="toggleSelectedAll"
                    :headers="headers"
                    :items="neuronList"
                    item-key="index"
                    show-select
                    dense
                    calculate-widths
                    :items-per-page="10"
                    mobile-breakpoint="0">
                        <template v-slot:item.name="{ item }">
                            <v-flex class="name-cell">{{ item.neuron.name }}</v-flex>
                        </template>
                    </v-data-table>
                </v-row>
                <v-row no-gutters>
                    <v-select
                    v-if="groupDialogOpen"
                    v-model="selectedGroup"
                    :items="groupList"
                    :label="$t('m.i.moveToGroup')"
                    >
                        <template v-slot:append-item>
                            <v-divider class="mb-2"></v-divider>
                            <v-list-item>
                                <v-text-field
                                :label="$t('m.i.addGroup')"
                                v-model="addGroupName"
                                :rules="rules"
                                @update:error="addGroupInputUpdateError"
                                @change="addGroupInputChanged"></v-text-field>
                            </v-list-item>
                        </template>
                        <template v-slot:item="{ item }">
                            <v-flex class="d-flex justify-space-between">
                                <span>{{item}}</span>
                                <v-icon @click.stop="removeGroup($event, item)">mdi-close</v-icon>
                            </v-flex>
                        </template>
                    </v-select>
                </v-row>
                <v-row no-gutters>
                    <nf-buttons
                    :btns="openCloseBtns"
                    @confirm="close(true)"
                    @cancel="close(false)"
                    ></nf-buttons>
                </v-row>
            </v-card>
        </template>
    </v-dialog>
</template>

<script>

export default {
    props: {
        groupList: {
            type: Array,
            default: () => []
        },
        neuronList: {
            type: Array,
            default: () => []
        },
        value: {
            type: Array,
            default: () => []
        },
    },
    data() {
        return {
            selectedNeurons: [],
            groupDialogOpen: false,
            openCloseBtns: [
                {label: this.$t("m.i.confirm"), col: 'grey', method: 'confirm'},
                {label: this.$t("m.i.cancel"), col: 'red', method: 'cancel'}
            ],
            selectedGroup: null,
            headers: [
                {
                    text: 'name',
                    value: 'name',
                    align: 'center',
                    width: 200,
                    groupable: false,
                    sortable: true
                },
            ],

            // "add group" text field variables
            addGroupName: "",
            rules: undefined,
            addGroupDuplicateErr: false,
        }
    },
    created() {
        var self = this;
        // rules: group names must be unique and not empty;
        this.rules = [
            value => value.length > 0 ? true : this.$t("m.a.groupNameCantBeBlank"),
            value => self.groupList.indexOf(value) < 0 ? true : this.$t("m.a.groupNameMustBeUnique")
        ]
    },
    methods: {
        open() {
            this.groupDialogOpen = true;
            this.selectedNeurons = this.value;
        },
        /**
         * in the original method vuetify offers,
         * toggle-select-all only selects/deselects all items in the current page
         */
        toggleSelectedAll(val) {
            if(val.value) {
                this.selectedNeurons = this.neuronList;
                this.neuronList.forEach(item => {
                    item.neuron.material.uniforms.isSelected.value = true;
                })
            }
            else{
                this.selectedNeurons = [];
                this.neuronList.forEach(item => {
                    item.neuron.material.uniforms.isSelected.value = false;
                })
            }
        },
        itemSelected(e) {
            this.setSelectMaterial(e.item, e.value);
        },
        /**
         * set material's isSelected value when item is selected/deselected
         */
        setSelectMaterial(item, val) {
            item.neuron.material.uniforms.isSelected.value = val;
        },
        removeGroup(event, groupName) {
            // check if this group contains any neuron
            for(let i = 0; i < this.neuronList.length; i++) {
                if(this.neuronList[i].group == groupName) {
                    this.$store.commit("alert", {
                        content: this.$t("m.a.mustClearBeforeDeleteGroup")
                    })
                    return;
                }
            }
            // okay to remove
            var index = this.groupList.indexOf(groupName);
            if (index >= 0) {
                this.groupList.splice(index, 1);
            }
        },
        addGroupInputUpdateError(val) {
            this.addGroupDuplicateErr = val;
        },
        addGroupInputChanged(newGroupName) {
            if(!this.addGroupDuplicateErr && newGroupName.length > 0) {
                // only adds to groupList when newGroupName is unique
                this.groupList.push(newGroupName);
                // clear text field
                this.addGroupName = "";
            }
        },
        /**
         * closes the dialog
         * @param doChange: applies group changes if true
         */
        close(doChange) {
            if(doChange) {
                // applies change
                if(this.selectedGroup) {
                    this.selectedNeurons.forEach(n => {
                        n.group = this.selectedGroup;
                    });
                }
            }
            this.groupDialogOpen = false;
            this.$emit("input", this.selectedNeurons);
        },
    },
}
</script>

<style scoped>
.group-dialog-card {
    width: min-content;
}
.group-table >>> .v-data-footer__select {
    display: none;
}
.group-table >>> .name-cell {
    width: 300px;
    text-align: left;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
</style>