import * as THREE from 'three';
import store from '../../store/store';

function getShader(shaderName) {
    try {
        var shader = require('./' + shaderName + '.glsl');
        return shader;
    }
    catch(err) {
        store.commit('alert', {
            content: '获取GLSL文件失败'
        });
        return null;
    }
}

function createMainMaterial(camera, idValue, lights, {
    primaryColor = 0xc0c0c0,
    secondaryColor = 0x000000,
    isSelected = false,
    type = 1.0,
    shininess = 16.0
}) {
    const vertexShader = getShader('MainVertexShader');
    const fragmentShader = getShader('MainFragmentShader');
    var customMaterial = new THREE.ShaderMaterial(
    {
        uniforms:
        {
            type:   { type: "f", value: type }, // glow plain flat
            viewVector: { type: "v3", value: camera.position },
            primaryColor: { type: "c", value: new THREE.Color(primaryColor) },
            secondaryColor: { type: "c", value: new THREE.Color(secondaryColor) },
            light1Color: { type: "c", value: lights[0].outColor },
            light2Color: { type: "c", value: lights[1].outColor },
            light3Color: { type: "c", value: lights[2].outColor },
            light2Dir: { type: "c", value: lights[1].direction },
            light3Dir: { type: "c", value: lights[2].direction },
            isSelected: { type: "b", value: isSelected },
            isInfo: { type: "b", value: false },
            idValue: { type: "b", value: idValue },
            shininess: { type: "f", value: shininess }
        },
        vertexShader:   vertexShader,
        fragmentShader: fragmentShader,
        side: THREE.FrontSide,
    }   );
    return customMaterial;
}


function createTagMaterial(scale, texture, idValue) {
    const vertexShader = getShader('TagVertexShader');
    const fragmentShader = getShader('TagFragmentShader');
    var customMaterial = new THREE.ShaderMaterial(
    {
        uniforms:
        {
            translation: { type: "c", value: new THREE.Vector3() },
            scale: { type: "v2", value: scale },
            u_texture: { type: "t", value: texture },
            isSelected: { type: "b", value: false },
            idValue: { type: "b", value: idValue },
            isInfo: { type: "b", value: false },
        },
        vertexShader:   vertexShader,
        fragmentShader: fragmentShader,
        side: THREE.FrontSide,
    }   );
    customMaterial.depthTest = false;
    customMaterial.depthWrite = false;
    return customMaterial;
}

export { getShader, createMainMaterial, createTagMaterial };