<template>
    <div class="right-bar-wrapper">
        <v-card class="right-bar-container" tile :elevation="5">
            <v-tooltip :left="isDesktop" :bottom="!isDesktop">
                <template v-slot:activator="{ on, attrs }">
                        <v-btn dark tile class="expand-retract-flex" v-bind="attrs"
                            v-on="on" @click.stop="expandRetract">
                            <v-icon dark>mdi-arrow-left-drop-circle
                                {{isDesktop ? 'mdi-arrow-left-drop-circle' : 'mdi-chevron-down-circle'}}
                                {{$store.state.panelOpen ? 'mdi-rotate-180' : undefined}}
                            </v-icon>
                        </v-btn>
                </template>
                <span>{{$store.state.panelOpen ? $t('m.i.fold') : $t('m.i.unfold')}}</span>
            </v-tooltip>

            <v-tabs :hide-slider="isDesktop" dense :vertical="isDesktop">
                <v-tooltip :left="isDesktop" :bottom="!isDesktop" v-for="(item, index) in displayedItems" :key="index">
                    <template v-slot:activator="{ on, attrs }">
                            <v-tab v-bind="attrs"
                                v-on="on" @click.stop="togglePanelWrappers(item.itemIndex)">
                                <v-icon>{{item.icon}}</v-icon>
                            </v-tab>
                    </template>
                    <span>{{item.tooltip}}</span>
                </v-tooltip>
            </v-tabs>
        </v-card>
        <aside
        class="panels-wrapper theme--light elevation-4"
        :class="{'panels-wrapper-open': $store.state.panelOpen}">
            <slot name="file" v-if="$store.state.panelIndex==0"></slot>
            <slot name="scene" v-if="$store.state.panelIndex==1"></slot>
            <slot name="draw" v-else-if="$store.state.panelIndex==2"></slot>
            <slot name="neurons" v-else-if="$store.state.panelIndex==3"></slot>
            <!-- <slot name="template" v-else-if="$store.state.panelIndex==4"></slot> -->
            <slot v-else></slot>
        </aside>
    </div>
</template>

<script>
export default {
    name: 'RightBar',
    props: {
        sceneIsSet: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            items: [
                {
                    icon: "mdi-file",
                    tooltip: this.$t("m.i.file"),
                    itemIndex: 0 // for the sake of changing the order of sidebar items dynamically
                },
                {
                    icon: "mdi-brain",
                    tooltip: this.$t("m.i.neuron"),
                    itemIndex: 3
                },
                {
                    icon: "mdi-draw",
                    tooltip: this.$t("m.i.draw"),
                    itemIndex: 2
                },
                {
                    icon: "mdi-image",
                    tooltip: this.$t("m.i.scene"),
                    itemIndex: 1
                },
                // {
                //     icon: "mdi-square-rounded",
                //     tooltip: "测试"
                // },
            ],
            documentWidth: 0,
        }
    },
    computed: {
        isDesktop() {
            return this.documentWidth > 800;
        },
        displayedItems() {
            return this.sceneIsSet ? this.items : this.items.slice(0, 1);
        }
    },
    mounted() {
        this.resized();
    },
    methods: {
        /**
         * when document is resized
         */
        resized() {
            this.documentWidth = document.body.clientWidth;
        },
        togglePanelWrappers(index) {
            this.$store.commit('setPanelStat', {
                // do NOT retract if another panels-wrapper is opened
                open: this.$store.state.panelIndex == index ? !this.$store.state.panelOpen : true,
                index: index
            })
        },
        expandRetract() {
            this.$store.commit('setPanelStat', {
                open: !this.$store.state.panelOpen,
            })
        }
    },
}
</script>

<style scoped>
@media screen and (min-width: 801px) {
.right-bar-wrapper {
    flex-direction: row-reverse;
    justify-content: end;
    height: 100%;
}
.right-bar-container {
    height: 100%;
    top: 0;
    right: 0;
    width: min-content;
}
.panels-wrapper {
    top: 0;
    left: 0;
    transform: translateX(0%);
    height: 100vh;
    width: 320px;
}
.panels-wrapper-open {
    transform: translateX(-100%);
}
}

@media screen and (max-width: 800px) {
.right-bar-wrapper {
    flex-direction: column;
    width: 100%;
}
.right-bar-container {
    display: flex;
    flex-direction: row-reverse;
    top: 0;
    left: 0;
    width: 100%;
}
.panels-wrapper {
    flex: 1;
    top: 0;
    left: 0;
    transform: translateY(-100%);
    width: 100%;
    height: 100vh;
}
.panels-wrapper >>> .right-panel-container {
    margin-top: 48px;
    height: calc(100% - 48px);
}
.panels-wrapper >>> .v-expansion-panel-content__wrap {
    padding-left: 48px;
    padding-right: 48px;
}
.panels-wrapper-open {
    transform: translateY(0%);
}
}

.right-bar-wrapper {
    position: absolute;
    display: flex;
    top: 0;
    right: 0;
}
.right-bar-container {
    position: relative;
    z-index: 20;
}
.v-tab {
    min-width: 48px;
    padding: 0;
}
.panels-wrapper {
    position: absolute;
    background: #fff;
    z-index: 10;
    transition-duration: 0.2s;
}
.panels-wrapper >>> .v-expansion-panel {
    border-radius: 0;
}
/* .panels-wrapper >>> .v-expansion-panel-header--active {
    background-color: #a0a0a0;
} */
.panels-wrapper >>> .v-expansion-panel-content__wrap {
    padding-bottom: 0;
}
.title-btn {
    margin-left: -80px !important;
    position: absolute;
}
.expand-retract-flex {
    height: 48px !important;
    width: 48px;
    min-width: 48px !important;
    padding: 0 !important;
}
</style>