<template>
    <v-snackbar
        v-model="snackbar"
        :timeout="timeout"
        color="red"
    >{{ content }}
    <template v-slot:action="{ attrs }">
        <v-btn
            color="white"
            icon
            v-bind="attrs"
            @click="snackbar = false"
        >
            <v-icon>mdi-close</v-icon>
        </v-btn>
        </template>
    </v-snackbar>
</template>

<script>
export default {
    data: () => ({
        snackbar: false,
        content: 'My timeout is set to 2000.',
        timeout: 3000,
    }),
    mounted() {
        this.$store.commit('setAlertOpenMethod', this.open);
    },
    methods: {
        open(stat) {
            this.content = stat.content;
            if(stat.timeout === undefined) {
                this.timeout = 3000;
            }
            else {
                this.timeout = stat.timeout;
            }
            this.snackbar = true;
        }
    }
}
</script>