<template>
    <v-data-table
        class="tag-table"
        v-model="selectedTags"
        @item-selected="itemSelected"
        @toggle-select-all="toggleSelectedAll"
        :headers="headers"
        :items="tagList"
        item-key="idValue"
        show-select
        dense
        mobile-breakpoint="0"
        disable-pagination
        hide-default-footer
    >
        <template v-slot:item.text="{ item }">
            <div class="text-display" :title="item.text">
                <input
                class="name-input"
                :value="item.text"
                disabled/>
            </div>
        </template>
        <template v-slot:item.settings="{ item }">
            <v-row no-gutters class="settings-btns-row justify-center">
                <v-icon
                :title="$t('m.i.draw')"
                @click="$emit('openModifyTagDialog', item)
                ">mdi-draw</v-icon>
                <v-icon
                :title="$t('m.i.center')"
                @click="focusClicked(item)">mdi-camera</v-icon>
            </v-row>
        </template>
    </v-data-table>
</template>

<script>
export default {
    props: {
        value: {
            default: () => [],
            type: Array
        },
        tagList: {
            type: Array,
            default: () => []
        }
    },
    data() {
        return {
            headers: [
                {
                    text: this.$t("m.i.text"),
                    value: 'text',
                    align: 'center',
                    width: 100,
                    groupable: false,
                    sortable: true,
                },
                {
                    text: this.$t("m.i.settings"),
                    value: 'settings',
                    align: 'center',
                    groupable: false,
                    sortable: false,
                },
            ],
            selectedTags: [],
        }
    },
    methods: {
        forceRefreshSelectedTags() {
            this.selectedTags = this.value;
        },
        itemSelected(e) {
            if(e) {
                this.setSelectMaterial(e.item, e.value);
            }
            this.$nextTick(()=> {
                this.$emit("input", this.selectedTags);
            })
        },
        /**
         * in the original method vuetify offers,
         * toggle-select-all only selects/deselects all items in the current page
         */
        toggleSelectedAll(val) {
            if(val.value) {
                this.selectedTags = this.tagList;
                this.tagList.forEach(item => {
                    this.setSelectMaterial(item, true);
                })
            }
            else{
                this.selectedTags = [];
                this.tagList.forEach(item => {
                    this.setSelectMaterial(item, false);
                })
            }
            this.itemSelected();
        },
        /**
         * set material's isSelected value when item is selected/deselected
         */
        setSelectMaterial(item, val) {
            item.sprite.material.uniforms.isSelected.value = val;
            this.valueChanged();
        },
        focusClicked(item) {
            this.$emit("focusOnItem", item);
        },
        valueChanged() {
            this.$emit("renderOnce");
        },
    }
}
</script>

<style scoped>
.tag-table {
    max-height: 480px;
    width: 750px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.tag-table >>> th, .tag-table >>> td {
    padding: 0 2px !important;
}
.tag-table >>> .v-data-table__wrapper {
    flex: 1;
    overflow-y: scroll;
    overflow-x: auto;
    padding: 5px 0;
}
.tag-table >>> .v-small-dialog__activator {
    width: 140px;
}
.tag-table >>> .text-display {
    max-width: 140px;
    display: block;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    text-align: left !important;
}
.settings-btns-row {
    flex-wrap: nowrap;
}
</style>