<template>
    <v-row :class="{'no-row': small}">
        <v-col cols="8" v-if="small == false" :class="{'text--disabled': disabled}">{{name}}</v-col>
        <v-col cols="4" v-if="small == false">
            <v-btn
            tile
            class="small-btn-2"
            :disabled="disabled"
            :color="threeToStr(value)"
            @click.stop="openColorController"></v-btn>
        </v-col>
        <v-icon
        v-else
        class="small-btn-3"
        :disabled="disabled"
        :color="threeToStr(value)"
        @click.stop="openColorController">mdi-alpha-c-box-outline</v-icon>
        
        <!-- color controller -->
        <v-dialog
            v-if="colorPanelOpen"
            v-model="colorPanelOpen"
            overlay-opacity="0"
            width="min-content"
            >
                <v-color-picker dark
                :value="threeToStr(value)"
                @update:color="colorControllerChanged"
                ></v-color-picker>
        </v-dialog>
    </v-row>
</template>

<script>
import * as THREE from 'three';
export default {
    props: {
        name: String,
        value: {
            type: Object,
            default: () => {
                return new THREE.Color(1.0, 0.0, 0.0);
            }
        },
        disabled: {
            type: Boolean,
            default: false,
        },
        small: {
            type: Boolean,
            default: false,
        }
    },
    data() {
        return {
            colorPanelOpen: false
        };
    },
    methods: {
        openColorController() {
            this.colorPanelOpen = true;
        },
        threeToStr(threeColor) {
            return '#' + threeColor.getHexString();
        },
        colorControllerChanged(val) {
            this.value.r = val.rgba.r / 255.0;
            this.value.g = val.rgba.g / 255.0;
            this.value.b = val.rgba.b / 255.0;
            this.$emit('input', this.value);
        }
    }
}
</script>

<style scoped>
.small-btn-2 {
    height: 100% !important;
    min-width: 100% !important;
}
.small-btn-3 {
    height: 21px !important;
    width: 21px !important;
    min-width: 21px !important;
}
.no-row {
    display: inline;
    margin: 0;
    flex: 0;
}
</style>